// OpenAI Configuration
export const OPENAI_CONFIG = {
    // API endpoint
    API_URL: "https://api.openai.com/v1/chat/completions",
    
    // Default model
    DEFAULT_MODEL: process.env.REACT_APP_OPENAI_MODEL || "gpt-3.5-turbo",
    
    // API key t environment variable
    API_KEY: process.env.REACT_APP_OPENAI_API_KEY,
    
    // Organization ID (optional)
    ORG_ID: process.env.REACT_APP_OPENAI_ORG_ID,
    
    // Default parameters
    DEFAULT_PARAMS: {
        temperature: 0.7,
        max_tokens: 1000,
        top_p: 1,
        frequency_penalty: 0,
        presence_penalty: 0
    },
    
    // System prompts for different use cases
    SYSTEM_PROMPTS: {
        CHAT: "Bn l mt tr l AI thn thin v hu ch. Hy tr li bng ting Vit mt cch t nhin v chnh xc.",
        CODE: "Bn l mt lp trnh vin chuyn nghip. Hy gip  vi cc vn  lp trnh v code review.",
        CREATIVE: "Bn l mt nh sng to ni dung. Hy gip to ra nhng  tng sng to v ni dung hp dn."
    }
};

// Helper function  to request headers
export const createOpenAIHeaders = () => {
    const headers = {
        "Content-Type": "application/json",
        "Authorization": `Bearer ${OPENAI_CONFIG.API_KEY}`
    };
    
    if (OPENAI_CONFIG.ORG_ID) {
        headers["OpenAI-Organization"] = OPENAI_CONFIG.ORG_ID;
    }
    
    return headers;
};

// Helper function  to request body
export const createOpenAIRequestBody = (messages, model = OPENAI_CONFIG.DEFAULT_MODEL, params = {}) => {
    return {
        model: model,
        messages: messages,
        ...OPENAI_CONFIG.DEFAULT_PARAMS,
        ...params
    };
};

// Helper function  gi request n OpenAI
export const sendOpenAIRequest = async (messages, model = OPENAI_CONFIG.DEFAULT_MODEL, params = {}) => {
    if (!OPENAI_CONFIG.API_KEY) {
        throw new Error("OpenAI API key cha c cu hnh. Vui lng thm REACT_APP_OPENAI_API_KEY vo file .env");
    }
    
    try {
        const response = await fetch(OPENAI_CONFIG.API_URL, {
            method: "POST",
            headers: createOpenAIHeaders(),
            body: JSON.stringify(createOpenAIRequestBody(messages, model, params))
        });
        
        if (!response.ok) {
            const errorData = await response.json();
            throw new Error(`OpenAI API Error: ${errorData.error?.message || response.statusText}`);
        }
        
        const data = await response.json();
        return data;
    } catch (error) {
        console.error("OpenAI API Error:", error);
        throw error;
    }
};

// Helper function  to chat message
export const createChatMessage = (role, content) => {
    return { role, content };
};

// Helper function  x l response
export const processOpenAIResponse = (data) => {
    if (data.choices && data.choices.length > 0) {
        return data.choices[0].message.content;
    }
    return "Xin li, ti khng th to phn hi.";
};
