// Simple email service using Formspree (free, no setup required)
const FORMSPREE_ENDPOINT = 'https://formspree.io/f/xyzdrepv';

/**
 * Gi email thng qua Formspree (min ph, khng cn cu hnh)
 * @param {Object} templateParams - Tham s template email
 * @param {string} templateParams.from_name - Tn ngi gi
 * @param {string} templateParams.from_email - Email ngi gi
 * @param {string} templateParams.phone_number - S in thoi
 * @param {string} templateParams.subject - Tiu 
 * @param {string} templateParams.message - Ni dung tin nhn
 * @param {string} templateParams.to_email - Email ngi nhn
 * @returns {Promise<Object>} - Kt qu gi email
 */
export const sendContactEmail = async (templateParams) => {
    try {
        // Validate required parameters
        if (!templateParams.from_name || !templateParams.message) {
            throw new Error('Tn v ni dung tin nhn l bt buc');
        }

        // Prepare email data for Formspree
        const emailData = {
            name: templateParams.from_name,
            email: templateParams.from_email || 'noreply@example.com',
            phone: templateParams.phone_number || '',
            subject: `${templateParams.subject || 'Lin h t website'} - ${templateParams.from_name}`,
            message: `
Thng tin lin h mi:

Tn: ${templateParams.from_name}
Email: ${templateParams.from_email || 'Khng cung cp'}
S in thoi: ${templateParams.phone_number || 'Khng cung cp'}
Tiu : ${templateParams.subject || 'Lin h t website'}

Ni dung tin nhn:
${templateParams.message}

---
Thi gian: ${new Date().toLocaleString('vi-VN')}
IP: ${window.location.hostname}
            `.trim(),
            _replyto: templateParams.from_email || 'noreply@example.com',
            _subject: `${templateParams.subject || 'Lin h t website'} - ${templateParams.from_name}`,
            _next: window.location.href
        };

        // Send email using Formspree
        const response = await fetch(FORMSPREE_ENDPOINT, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify(emailData)
        });

        if (response.ok) {
            console.log('Email sent successfully');
            return {
                success: true,
                message: 'Email  c gi thnh cng!',
                result: response
            };
        } else {
            throw new Error(`HTTP error! status: ${response.status}`);
        }

    } catch (error) {
        console.error('Error sending email:', error);
        return {
            success: false,
            message: `Li khi gi email: ${error.message}`,
            error: error
        };
    }
};

/**
 * Gi email bo co li hoc phn hi
 * @param {Object} params - Tham s email
 * @returns {Promise<Object>} - Kt qu gi email
 */
export const sendReportEmail = async (params) => {
    try {
        const emailData = {
            name: params.from_name || 'System',
            email: params.from_email || 'system@example.com',
            subject: `${params.subject || 'Bo co t h thng'} - ${params.from_name || 'System'}`,
            message: `
Bo co t h thng:

Tn: ${params.from_name || 'System'}
Email: ${params.from_email || 'system@example.com'}
S in thoi: ${params.phone_number || 'Khng cung cp'}
Loi bo co: ${params.report_type || 'general'}

Ni dung bo co:
${params.message}

---
Thi gian: ${new Date().toLocaleString('vi-VN')}
IP: ${window.location.hostname}
            `.trim(),
            _replyto: params.from_email || 'system@example.com',
            _subject: `${params.subject || 'Bo co t h thng'} - ${params.from_name || 'System'}`,
            _next: window.location.href
        };

        const response = await fetch(FORMSPREE_ENDPOINT, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(emailData)
        });

        const result = await response.json();

        if (response.ok && result.success) {
            return {
                success: true,
                message: 'Bo co  c gi thnh cng!',
                result: result
            };
        } else {
            throw new Error(result.message || `HTTP error! status: ${response.status}`);
        }

    } catch (error) {
        console.error('Error sending report email:', error);
        return {
            success: false,
            message: `Li khi gi bo co: ${error.message}`,
            error: error
        };
    }
};

/**
 * Test kt ni email service
 * @returns {Promise<Object>} - Kt qu test
 */
export const testEmailConnection = async () => {
    try {
        const testData = {
            name: 'Test User',
            email: 'test@example.com',
            subject: 'Test Email - Test User',
            message: `
Test kt ni email service:

Tn: Test User
Email: test@example.com
S in thoi: 0123456789

Ni dung test:
y l email test t h thng

---
Thi gian: ${new Date().toLocaleString('vi-VN')}
IP: ${window.location.hostname}
            `.trim(),
            _replyto: 'test@example.com',
            _subject: 'Test Email - Test User',
            _next: window.location.href
        };

        const response = await fetch(FORMSPREE_ENDPOINT, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(testData)
        });

        const result = await response.json();

        if (response.ok && result.success) {
            return {
                success: true,
                message: 'Kt ni Web3Forms service thnh cng!',
                result: result
            };
        } else {
            throw new Error(result.message || `HTTP error! status: ${response.status}`);
        }

    } catch (error) {
        console.error('Web3Forms service connection test failed:', error);
        return {
            success: false,
            message: `Li kt ni Web3Forms service: ${error.message}`,
            error: error
        };
    }
};

/**
 * Cu hnh email service
 * @param {string} endpoint - Formspree endpoint
 */
export const configureEmailService = (endpoint) => {
    FORMSPREE_ENDPOINT = endpoint;
    console.log('Email service configured successfully');
};

export default {
    sendContactEmail,
    sendReportEmail,
    testEmailConnection,
    configureEmailService
};