import React, { useState, useEffect } from 'react';
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn, MDBCheckbox, MDBIcon, MDBSpinner, MDBCard, MDBCardHeader, MDBCardBody } from 'mdb-react-ui-kit';
import { Link, useNavigate } from 'react-router-dom';
import { showAlert } from "../service/AlertServices";
import bcrypt from 'bcryptjs';
import TurnstileWidget from './TurnstileWidget';
import Honeypot from './Honeypot';
import { getAccounts } from '../api/Accounts';
import AuthService from '../service/AuthService';

function LoginPage() {
    const [accounts, setAccounts] = useState([]);
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [showPassword, setShowPassword] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [rememberMe, setRememberMe] = useState(false);
    const [errors, setErrors] = useState({});
    const [turnstileToken, setTurnstileToken] = useState('');
    const [turnstileResetKey, setTurnstileResetKey] = useState(0);
    const nav = useNavigate();

    // Ly d liu accounts t API
    useEffect(() => {
        async function fetchData() {
            const data = await getAccounts();
            setAccounts(data);
        }
        fetchData();
    }, []);

    // Load saved username nu c
    useEffect(() => {
        const savedUsername = localStorage.getItem("savedUsername");
        const rememberMeStatus = localStorage.getItem("rememberMe");
        if (savedUsername && rememberMeStatus === "true") {
            setUsername(savedUsername);
            setRememberMe(true);
        }
    }, []);


    const validateForm = () => {
        const newErrors = {};
        
        if (!username.trim()) {
            newErrors.username = 'Vui lng nhp tn ng nhp';
        }
        
        // Debug: Check turnstile token
        console.log('Turnstile token in validateForm:', turnstileToken);
        
        // Always require Turnstile verification for login
        if (!turnstileToken) {
            newErrors.turnstile = 'Vui lng xc minh bn khng phi l robot';
        }
        
        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    const handleLogin = async () => {
        if (!validateForm()) {
            return;
        }

        // Rate limiting check
        try {
            AuthService.canAttemptLogin();
        } catch (error) {
            showAlert(error.message, "error");
            return;
        }

        setIsLoading(true);
        
        try {
            // Tm ti khon theo username
            const found = accounts.find(acc => acc.username === username);
            if (!found) {
                AuthService.recordLoginAttempt(false);
                showAlert("Sai ti khon hoc mt khu", "error");
                setTurnstileToken('');
                setTurnstileResetKey(prev => prev + 1);
                return;
            }

            if (!found.status) {
                AuthService.recordLoginAttempt(false);
                showAlert("Ti khon  b cm, vui lng lin h qun tr vin!", "danger");
                setTurnstileToken('');
                setTurnstileResetKey(prev => prev + 1);
                return;
            }

            if (found.isApprove === false) {
                AuthService.recordLoginAttempt(false);
                showAlert("Ti khon ca bn cha c admin duyt. Vui lng ch ph duyt hoc lin h qun tr vin!", "warning");
                setTurnstileToken('');
                setTurnstileResetKey(prev => prev + 1);
                return;
            }

            const isPasswordCorrect = await bcrypt.compare(password, found.password);
            if (isPasswordCorrect) {
                // Record successful login attempt
                AuthService.recordLoginAttempt(true);
                
                // Create secure session
                AuthService.generateSessionToken(found);
                
                showAlert("ng nhp thnh cng", "success");
                
                // Update old localStorage for compatibility
                localStorage.setItem("user", JSON.stringify(found));
                localStorage.setItem("authenticated", "true");
                
                if (rememberMe) {
                    localStorage.setItem("rememberMe", "true");
                    localStorage.setItem("savedUsername", username);
                } else {
                    localStorage.removeItem("rememberMe");
                    localStorage.removeItem("savedUsername");
                }
                
                // Dispatch custom event to update Menu component
                window.dispatchEvent(new CustomEvent('userUpdated'));
                
                nav('/');
            } else {
                AuthService.recordLoginAttempt(false);
                showAlert("Sai ti khon hoc mt khu", "error");
                setTurnstileToken('');
                setTurnstileResetKey(prev => prev + 1);
            }
        } catch (error) {
            AuthService.recordLoginAttempt(false);
            showAlert("C li xy ra khi ng nhp", "error");
            setTurnstileToken('');
            setTurnstileResetKey(prev => prev + 1);
        } finally {
            setIsLoading(false);
        }
    };

    // X l phm Enter
    const handleKeyPress = (e) => {
        if (e.key === 'Enter') {
            handleLogin();
        }
    };

    // Toggle password visibility
    const togglePasswordVisibility = () => {
        setShowPassword(!showPassword);
    };


    return (
        <>
            <Honeypot onBotDetected={(info) => {
                console.log('Bot detected in login:', info);
                // Could block login or add extra verification
            }} />
            <div className="min-vh-100 d-flex align-items-center" style={{ 
                background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                padding: '20px 0'
            }}>
                <MDBContainer fluid className="px-3">
                {/* Header vi nt v home */}
                <div className="text-center mb-4">
                    <Link to="/" className="text-decoration-none">
                        <MDBBtn
                            color="light"
                            className="d-inline-flex align-items-center shadow-sm"
                            style={{
                                borderRadius: '25px',
                                padding: '10px 25px',
                                fontWeight: '600',
                                transition: 'all 0.3s ease',
                                border: 'none'
                            }}
                        >
                            <MDBIcon fas icon="home" className="me-2" />
                            V Trang Ch
                        </MDBBtn>
                    </Link>
                </div>

                <MDBRow className="justify-content-center">
                    <MDBCol xs="12" sm="10" md="8" lg="6" xl="5">
                        <MDBCard className="shadow-lg" style={{ borderRadius: '20px', overflow: 'hidden' }}>
                            {/* Card Header */}
                            <MDBCardHeader 
                                className="text-center py-4" 
                                style={{ 
                                    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                                    border: 'none'
                                }}
                            >
                                <MDBIcon icon="user-circle" size="3x" className="text-white mb-3" />
                                <h2 className="text-white mb-0 fw-bold">ng Nhp</h2>
                                <p className="text-white-50 mb-0">Cho mng bn quay tr li!</p>
                            </MDBCardHeader>

                            <MDBCardBody className="p-4 p-md-5">
                                {/* Form */}
                                <form onSubmit={(e) => { e.preventDefault(); handleLogin(); }}>
                                    {/* Username Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="user" className="me-2 text-primary" />
                                            Tn ng nhp
                                        </label>
                                        <MDBInput
                                            wrapperClass='mb-2'
                                            id='username'
                                            type='text'
                                            value={username}
                                            onChange={(e) => {
                                                setUsername(e.target.value);
                                                if (errors.username) {
                                                    setErrors({...errors, username: ''});
                                                }
                                            }}
                                            onKeyPress={handleKeyPress}
                                            placeholder="Nhp tn ng nhp ca bn..."
                                            className={errors.username ? 'is-invalid' : ''}
                                        />
                                        {errors.username && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.username}
                                            </div>
                                        )}
                                    </div>
                                    
                                    {/* Password Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="lock" className="me-2 text-primary" />
                                            Mt khu
                                        </label>
                                        <div className="position-relative">
                                            <MDBInput
                                                wrapperClass='mb-2'
                                                id='password'
                                                type={showPassword ? 'text' : 'password'}
                                                value={password}
                                                onChange={(e) => {
                                                    setPassword(e.target.value);
                                                    if (errors.password) {
                                                        setErrors({...errors, password: ''});
                                                    }
                                                }}
                                                onKeyPress={handleKeyPress}
                                                placeholder="Nhp mt khu ca bn..."
                                                className={errors.password ? 'is-invalid' : ''}
                                            />
                                            <button
                                                type="button"
                                                className="btn btn-link position-absolute"
                                                style={{
                                                    right: '15px',
                                                    top: '50%',
                                                    transform: 'translateY(-50%)',
                                                    border: 'none',
                                                    background: 'none',
                                                    padding: '0',
                                                    zIndex: '10',
                                                    color: '#6c757d'
                                                }}
                                                onClick={togglePasswordVisibility}
                                            >
                                                <MDBIcon 
                                                    icon={showPassword ? 'eye-slash' : 'eye'} 
                                                    size="lg"
                                                />
                                            </button>
                                        </div>
                                        {errors.password && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.password}
                                            </div>
                                        )}
                                    </div>

                                    {/* Remember Me */}
                                    <div className="d-flex justify-content-between align-items-center mb-4">
                                        <MDBCheckbox 
                                            name='rememberMe' 
                                            id='rememberMe' 
                                            label='Ghi nh ng nhp'
                                            checked={rememberMe}
                                            onChange={(e) => setRememberMe(e.target.checked)}
                                        />
                                        <Link to="#" className="text-decoration-none text-primary small">
                                            Qun mt khu?
                                        </Link>
                                    </div>

                                    {/* Turnstile Verification */}
                                    <div className="mb-4">
                                        <TurnstileWidget 
                                            onVerify={(token) => {
                                                console.log('LoginPage - Turnstile verified with token:', token);
                                                setTurnstileToken(token);
                                            }}
                                            onExpire={() => {
                                                console.log('LoginPage - Turnstile expired');
                                                setTurnstileToken('');
                                                setErrors({...errors, turnstile: 'Phin xc minh  ht hn, vui lng th li'});
                                            }}
                                            onError={() => {
                                                console.log('LoginPage - Turnstile error');
                                                setTurnstileToken('');
                                                setErrors({...errors, turnstile: 'Li xc minh, vui lng th li'});
                                            }}
                                            resetKey={turnstileResetKey}
                                        />
                                        {errors.turnstile && (
                                            <div className="text-danger small mt-2">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.turnstile}
                                            </div>
                                        )}
                                    </div>

                                    {/* Login Button */}
                                    <MDBBtn 
                                        className='w-100 mb-4 py-3' 
                                        size='lg' 
                                        onClick={handleLogin}
                                        disabled={isLoading}
                                        style={{ borderRadius: '10px', fontWeight: '600' }}
                                    >
                                        {isLoading ? (
                                            <>
                                                <MDBSpinner size="sm" className="me-2" />
                                                ang ng nhp...
                                            </>
                                        ) : (
                                            <>
                                                <MDBIcon icon="sign-in-alt" className="me-2" />
                                                ng Nhp
                                            </>
                                        )}
                                    </MDBBtn>
                                </form>

                                {/* Divider */}
                                {/* <div className="text-center mb-4">
                                    <hr className="my-3" />
                                    <span className="text-muted small bg-white px-3">Hoc</span>
                                </div> */}

                                {/* Social Login */}
                                {/* <div className="text-center mb-4">
                                    <p className="text-muted small mb-3">ng nhp bng ti khon x hi</p>
                                    <div className="d-flex justify-content-center gap-3">
                                        <MDBBtn 
                                            tag='a' 
                                            color='light' 
                                            className='rounded-circle shadow-sm'
                                            style={{ width: '45px', height: '45px', padding: '0' }}
                                        >
                                            <MDBIcon fab icon='google' style={{color: '#db4437'}}/>
                                        </MDBBtn>
                                        <MDBBtn 
                                            tag='a' 
                                            color='light' 
                                            className='rounded-circle shadow-sm'
                                            style={{ width: '45px', height: '45px', padding: '0' }}
                                        >
                                            <MDBIcon fab icon='facebook-f' style={{color: '#3b5998'}}/>
                                        </MDBBtn>
                                        <MDBBtn 
                                            tag='a' 
                                            color='light' 
                                            className='rounded-circle shadow-sm'
                                            style={{ width: '45px', height: '45px', padding: '0' }}
                                        >
                                            <MDBIcon fab icon='github' style={{color: '#333'}}/>
                                        </MDBBtn>
                                    </div>
                                </div> */}

                                {/* Register Link */}
                                <div className="text-center">
                                    <p className="text-muted mb-0">
                                        Cha c ti khon? 
                                        <Link to="/register" className="text-primary text-decoration-none fw-bold ms-1">
                                            ng k ngay
                                        </Link>
                                    </p>
                                </div>
                            </MDBCardBody>
                        </MDBCard>

                        {/* Features Card */}
                        <MDBCard className="mt-4 shadow-sm" style={{ borderRadius: '15px' }}>
                            <MDBCardBody className="p-4">
                                <h5 className="text-center text-primary mb-3">
                                    <MDBIcon icon="star" className="me-2" />
                                    Tnh nng ni bt
                                </h5>
                                <MDBRow>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="images" size="2x" className="text-primary mb-2" />
                                        <h6 className="text-dark">To nh hng lot</h6>
                                        <small className="text-muted">To nhiu nh cng lc</small>
                                    </MDBCol>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="download" size="2x" className="text-success mb-2" />
                                        <h6 className="text-dark">Ti xung nhanh</h6>
                                        <small className="text-muted">H tr nhiu nh dng</small>
                                    </MDBCol>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="mobile-alt" size="2x" className="text-info mb-2" />
                                        <h6 className="text-dark">Responsive</h6>
                                        <small className="text-muted">Hot ng mi thit b</small>
                                    </MDBCol>
                                </MDBRow>
                            </MDBCardBody>
                        </MDBCard>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
            </div>
        </>
    );
}

export default LoginPage;
