import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { MDBContainer, MDBRow, MDBCol, MDBBtn, MDBIcon } from 'mdb-react-ui-kit';
import './HomePage.css';
// Import nh nn
const bgImage = process.env.PUBLIC_URL + '/img/bg.jpg';

function HomePage() {
    const [isVisible, setIsVisible] = useState(false);
    const [currentFeature, setCurrentFeature] = useState(0);

    useEffect(() => {
        // Trigger animation after component mounts
        const timer = setTimeout(() => {
            setIsVisible(true);
        }, 100);
        return () => clearTimeout(timer);
    }, []);

    // Auto-rotate features
    useEffect(() => {
        const interval = setInterval(() => {
            setCurrentFeature(prev => (prev + 1) % features.length);
        }, 3000);
        return () => clearInterval(interval);
    }, []);

    const features = [
        {
            icon: 'image',
            title: 'AI Image Generator',
            description: 'To nh ngh thut vi AI mnh m',
            color: 'primary',
            path: '/sim-generator'
        },
        {
            icon: 'comments',
            title: 'Chat vi AI',
            description: 'Tr chuyn thng minh vi AI Assistant',
            color: 'success',
            path: '/chat'
        },
        {
            icon: 'envelope',
            title: 'Lin h',
            description: 'Kt ni v h tr t i ng chuyn nghip',
            color: 'info',
            path: '/contact'
        }
    ];

    const stats = [
        { number: '10K+', label: 'nh  to' },
        { number: '5K+', label: 'Ngi dng' },
        { number: '99%', label: 'Hi lng' },
        { number: '24/7', label: 'H tr' }
    ];

    return (
        <div className="homepage">
            {/* Hero Section */}
            <section className="hero-section">
                <div 
                    className="hero-background"
                    style={{
                        backgroundImage: `url(${bgImage})`,
                        backgroundSize: 'cover',
                        backgroundPosition: 'center',
                        backgroundRepeat: 'no-repeat'
                    }}
                >
                    <div className="hero-overlay"></div>
                </div>
                
                <MDBContainer className="hero-content">
                    <MDBRow className="align-items-center min-vh-100">
                        <MDBCol lg="6" className="text-center text-lg-start">
                            <div className={`hero-text ${isVisible ? 'animate-fade-in-up' : ''}`}>
                                <h1 className="hero-title">
                                    Cho mng n vi
                                    <span className="gradient-text"> SIM Platform</span>
                                </h1>
                                <p className="hero-subtitle">
                                    Nn tng AI tin tin vi kh nng to nh v tr chuyn thng minh. 
                                    Khm ph sc mnh ca tr tu nhn to ngay hm nay.
                                </p>
                                <div className="hero-buttons">
                                    <MDBBtn
                                        color="primary"
                                        size="lg"
                                        className="me-3 mb-3 hero-btn"
                                        tag={Link}
                                        to="/sim-generator"
                                    >
                                        <MDBIcon icon="image" className="me-2" />
                                        Bt u to nh
                                    </MDBBtn>
                                    <MDBBtn
                                        color="outline-light"
                                        size="lg"
                                        className="mb-3 hero-btn"
                                        tag={Link}
                                        to="/chat"
                                    >
                                        <MDBIcon icon="comments" className="me-2" />
                                        Chat vi AI
                                    </MDBBtn>
                                </div>
                            </div>
                        </MDBCol>
                        
                        <MDBCol lg="6" className="text-center">
                            <div className={`hero-visual ${isVisible ? 'animate-fade-in-right' : ''}`}>
                                <div className="floating-cards">
                                    <div className="card-1">
                                        <MDBIcon icon="image" size="3x" className="text-primary" />
                                        <p>AI Generator</p>
                                    </div>
                                    <div className="card-2">
                                        <MDBIcon icon="comments" size="3x" className="text-success" />
                                        <p>AI Chat</p>
                                    </div>
                                    <div className="card-3">
                                        <MDBIcon icon="cog" size="3x" className="text-info" />
                                        <p>Smart Tools</p>
                                    </div>
                                </div>
                            </div>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
            </section>

            {/* Features Section */}
            <section className="features-section py-5">
                <MDBContainer>
                    <div className="text-center mb-5">
                        <h2 className={`section-title ${isVisible ? 'animate-fade-in-up' : ''}`}>
                            Tnh nng ni bt
                        </h2>
                        <p className="section-subtitle">
                            Khm ph cc cng c AI mnh m c thit k cho bn
                        </p>
                    </div>
                    
                    <MDBRow>
                        {features.map((feature, index) => (
                            <MDBCol md="4" key={index} className="mb-4">
                                <div 
                                    className={`feature-card ${isVisible ? 'animate-fade-in-up' : ''}`}
                                    style={{ animationDelay: `${index * 0.2}s` }}
                                >
                                    <div className="feature-icon">
                                        <MDBIcon 
                                            icon={feature.icon} 
                                            size="3x" 
                                            className={`text-${feature.color}`}
                                        />
                                    </div>
                                    <h4 className="feature-title">{feature.title}</h4>
                                    <p className="feature-description">{feature.description}</p>
                                    <MDBBtn
                                        color={feature.color}
                                        tag={Link}
                                        to={feature.path}
                                        className="feature-btn"
                                    >
                                        Khm ph ngay
                                        <MDBIcon icon="arrow-right" className="ms-2" />
                                    </MDBBtn>
                                </div>
                            </MDBCol>
                        ))}
                    </MDBRow>
                </MDBContainer>
            </section>

            {/* Stats Section */}
            <section className="stats-section py-5">
                <MDBContainer>
                    <MDBRow>
                        {stats.map((stat, index) => (
                            <MDBCol md="3" key={index} className="text-center mb-4">
                                <div 
                                    className={`stat-item ${isVisible ? 'animate-fade-in-up' : ''}`}
                                    style={{ animationDelay: `${index * 0.1}s` }}
                                >
                                    <h3 className="stat-number">{stat.number}</h3>
                                    <p className="stat-label">{stat.label}</p>
                                </div>
                            </MDBCol>
                        ))}
                    </MDBRow>
                </MDBContainer>
            </section>

            {/* CTA Section */}
            <section className="cta-section py-5">
                <MDBContainer>
                    <div className="text-center">
                        <div className={`cta-content ${isVisible ? 'animate-fade-in-up' : ''}`}>
                            <h2 className="cta-title">Sn sng bt u?</h2>
                            <p className="cta-subtitle">
                                Tham gia cng hng nghn ngi dng ang s dng SIM Platform
                            </p>
                            <MDBBtn
                                color="primary"
                                size="lg"
                                className="cta-btn"
                                tag={Link}
                                to="/sim-generator"
                            >
                                <MDBIcon icon="rocket" className="me-2" />
                                Bt u ngay
                            </MDBBtn>
                        </div>
                    </div>
                </MDBContainer>
            </section>
        </div>
    );
}

export default HomePage;
