import React, { useState, useRef, useCallback } from 'react';
import { Stage, Layer, Image, Text, Group, Line, Rect } from 'react-konva';
import useImage from 'use-image';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';
import { showAlert } from '../service/AlertServices';
import Konva from 'konva';
import './SimImageGenerator.css';

const SimImageGenerator = () => {
    const [backgroundImage, setBackgroundImage] = useState(null);
    const [simList, setSimList] = useState('');
    const [simData, setSimData] = useState([]);
    const [imageFile, setImageFile] = useState(null);
    const [textConfig, setTextConfig] = useState({
        simNumber: { 
            x: 100, y: 100, 
            fontSize: 24, 
            color: '#000000',
            fontFamily: 'Arial',
            fontStyle: 'normal',
            fontWeight: 'normal',
            stroke: '#ffffff',
            strokeWidth: 0,
            shadowColor: '#000000',
            shadowBlur: 2,
            shadowOffset: { x: 1, y: 1 },
            opacity: 1
        },
        price: { 
            x: 100, y: 150, 
            fontSize: 20, 
            color: '#ff0000',
            fontFamily: 'Arial',
            fontStyle: 'normal',
            fontWeight: 'bold',
            stroke: '#ffffff',
            strokeWidth: 1,
            shadowColor: '#000000',
            shadowBlur: 2,
            shadowOffset: { x: 1, y: 1 },
            opacity: 1
        }
    });
    const [isGenerating, setIsGenerating] = useState(false);
    const [progress, setProgress] = useState(0);
    const [selectedText, setSelectedText] = useState('simNumber'); // 'simNumber', 'price', hoc 'custom'
    const [customTexts, setCustomTexts] = useState([]); // Mng cc text ty chnh
    const [newTextContent, setNewTextContent] = useState(''); // Ni dung text mi
    const [downloadFormat, setDownloadFormat] = useState('zip'); // 'zip', 'individual', hoc 'onebyone'
    const [snapEnabled, setSnapEnabled] = useState(true); // Bt/tt auto snap
    const [showSnapGuides, setShowSnapGuides] = useState(false); // Hin th guide lines
    const [selectedTextId, setSelectedTextId] = useState(null); // ID ca text ang c chn
    const [duplicateSims, setDuplicateSims] = useState([]); // Danh sch SIM trng lp
    const [showDuplicateChecker, setShowDuplicateChecker] = useState(false); // Hin th cng c kim tra trng
    const [imageQuality, setImageQuality] = useState(2); // Cht lng nh: 1=thng, 2=cao, 3=rt cao
    const [isDragOverPreview, setIsDragOverPreview] = useState(false); // Trng thi drag over preview
    const stageRef = useRef(null);
    const fileInputRef = useRef(null); // Ref cho hidden file input
    const [image] = useImage(backgroundImage);

    // X l upload nh - cp nht  h tr drag and drop
    const handleImageUpload = (file) => {
        if (file) {
            setImageFile(file);
            const reader = new FileReader();
            reader.onload = (e) => {
                setBackgroundImage(e.target.result);
            };
            reader.readAsDataURL(file);
        }
    };

    // X l drag t preview vo canvas
    const handlePreviewDragToCanvas = useCallback((imageData) => {
        // C th thm logic x l khi ko nh t preview vo canvas
        // V d: thm nh vo canvas, to layer mi, etc.
        console.log('Dragged image to canvas:', imageData);
        showAlert(` ko nh "${imageData.file.name}" vo canvas!`, 'info');
    }, []);

    // X l drag and drop file vo preview area
    const handlePreviewDragOver = useCallback((e) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragOverPreview(true);
    }, []);

    const handlePreviewDragLeave = useCallback((e) => {
        e.preventDefault();
        e.stopPropagation();
        // Ch clear khi ri khi preview area hon ton
        if (!e.currentTarget.contains(e.relatedTarget)) {
            setIsDragOverPreview(false);
        }
    }, []);

    const handlePreviewDrop = useCallback((e) => {
        e.preventDefault();
        e.stopPropagation();
        setIsDragOverPreview(false);

        const files = e.dataTransfer.files;
        if (files.length > 0) {
            const file = files[0]; // Ch ly file u tin
            if (file && file.type.startsWith('image/')) {
                handleImageUpload(file);
            } else {
                showAlert('Vui lng chn file nh!', 'warning');
            }
        }
    }, [handleImageUpload]);

    // X l click  chn file
    const handlePreviewClick = useCallback(() => {
        if (fileInputRef.current) {
            fileInputRef.current.click();
        }
    }, []);

    // X l file input change
    const handleFileInputChange = useCallback((e) => {
        const file = e.target.files[0];
        if (file) {
            handleImageUpload(file);
        }
    }, [handleImageUpload]);

    // X l danh sch SIM
    const handleSimListChange = (event) => {
        const value = event.target.value;
        setSimList(value);
        
        // Parse danh sch SIM t text vi nhiu nh dng phn tch
        const lines = value.split('\n').filter(line => line.trim());
        const parsedData = lines.map((line, index) => {
            // H tr nhiu nh dng: du phy, tab, du =, du -
            const separators = [',', '\t', '=', '-'];
            let parts = [];
            let usedSeparator = '';
            
            // Tm separator ph hp
            for (const sep of separators) {
                if (line.includes(sep)) {
                    parts = line.split(sep).map(part => part.trim());
                    usedSeparator = sep;
                    break;
                }
            }
            
            // Nu khng tm thy separator, coi nh ch c s SIM
            if (parts.length === 0) {
                parts = [line.trim()];
            }
            
            // Lm sch d liu
            const simNumber = parts[0] || '';
            let price = parts[1] || '';
            
            // X l gi tin: loi b k t khng cn thit
            if (price) {
                // Loi b du phy, du chm trong s
                price = price.replace(/[,.]/g, '');
                // Ch gi li s
                price = price.replace(/\D/g, '');
            }
            
            return {
                simNumber: simNumber,
                price: price,
                originalLine: line,
                lineNumber: index + 1,
                separator: usedSeparator
            };
        });
        setSimData(parsedData);
    };

    // Helper function  tnh kch thc text
    const calculateTextSize = (textConfig, textContent) => {
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        const fontString = `${textConfig.fontStyle} ${textConfig.fontWeight} ${textConfig.fontSize}px ${textConfig.fontFamily}`;
        ctx.font = fontString;
        const textMetrics = ctx.measureText(textContent);
        return {
            width: textMetrics.width,
            height: textConfig.fontSize * 0.8
        };
    };

    // Tnh ton v tr snap (auto cn chnh) -  tt hon ton
    const calculateSnapPosition = (x, y, textWidth = 0, textHeight = 0) => {
        // Tt hon ton chc nng auto snap
        return { x, y };
    };

    // Cp nht v tr text khi ko th
    const handleTextDrag = (type, newPos) => {
        setTextConfig(prev => ({
            ...prev,
            [type]: {
                ...prev[type],
                x: Math.round(newPos.x),
                y: Math.round(newPos.y)
            }
        }));
    };

    // Cp nht v tr text khi thay i input
    const handleTextPositionChange = (type, property, value) => {
        setTextConfig(prev => ({
            ...prev,
            [type]: {
                ...prev[type],
                [property]: parseInt(value) || 0
            }
        }));
    };

    // Cp nht style text
    const handleTextStyleChange = (type, property, value) => {
        setTextConfig(prev => ({
            ...prev,
            [type]: {
                ...prev[type],
                [property]: value
            }
        }));
    };

    // X l click vo text  chn
    const handleTextClick = (textType, customIndex = null) => {
        if (textType === 'custom' && customIndex !== null) {
            setSelectedText(`custom_${customIndex}`);
            setSelectedTextId(`custom_${customIndex}`);
        } else {
            setSelectedText(textType);
            setSelectedTextId(textType);
        }
    };

    // Thm text mi
    const addCustomText = () => {
        if (!newTextContent.trim()) {
            showAlert('Vui lng nhp ni dung text!', 'warning');
            return;
        }

        const newText = {
            id: Date.now(),
            content: newTextContent,
            x: 50,
            y: 200,
            fontSize: 20,
            color: '#000000',
            fontFamily: 'Arial',
            fontStyle: 'normal',
            fontWeight: 'normal',
            stroke: '#ffffff',
            strokeWidth: 0,
            shadowColor: '#000000',
            shadowBlur: 2,
            shadowOffset: { x: 1, y: 1 },
            opacity: 1
        };

        setCustomTexts(prev => [...prev, newText]);
        setSelectedText(`custom_${newText.id}`);
        setNewTextContent('');
        showAlert(' thm text mi!', 'success');
    };

    // Xa text ty chnh
    const removeCustomText = (textId) => {
        setCustomTexts(prev => prev.filter(text => text.id !== textId));
        if (selectedText === `custom_${textId}`) {
            setSelectedText('simNumber');
            setSelectedTextId('simNumber');
        }
        showAlert(' xa text!', 'success');
    };

    // Cp nht text ty chnh
    const updateCustomText = (textId, property, value) => {
        setCustomTexts(prev => prev.map(text => 
            text.id === textId ? { ...text, [property]: value } : text
        ));
    };

    // Cp nht v tr custom text
    const updateCustomTextPosition = (textId, newPos) => {
        setCustomTexts(prev => prev.map(text => 
            text.id === textId ? { 
                ...text, 
                x: Math.round(newPos.x), 
                y: Math.round(newPos.y) 
            } : text
        ));
    };

    // To border rectangle cho text c chn vi kch thc chnh xc
    const createSelectionBorder = (textConfig, textContent = '') => {
        const padding = 4; // Padding nh  border st text
        
        // Tnh ton kch thc text da trn ni dung v font
        const canvas = document.createElement('canvas');
        const ctx = canvas.getContext('2d');
        
        // Cu hnh font ging nh text thc t
        const fontString = `${textConfig.fontStyle} ${textConfig.fontWeight} ${textConfig.fontSize}px ${textConfig.fontFamily}`;
        ctx.font = fontString;
        
        // o kch thc text thc t
        const textMetrics = ctx.measureText(textContent || 'Sample Text');
        const textWidth = textMetrics.width;
        
        // c tnh chiu cao text da trn font size
        // S dng h s nh hn  border st text hn
        const textHeight = textConfig.fontSize * 0.8; // Gim h s  st text hn
        
        return {
            x: textConfig.x - padding,
            y: textConfig.y - padding,
            width: textWidth + (padding * 2),
            height: textHeight + (padding * 2),
            stroke: '#007bff',
            strokeWidth: 2,
            dash: [5, 5],
            opacity: 0.8,
            fill: 'rgba(0, 123, 255, 0.1)'
        };
    };

    // Kim tra SIM trng lp
    const checkDuplicateSims = () => {
        if (!simData.length) {
            showAlert('Vui lng nhp danh sch SIM trc!', 'warning');
            return;
        }

        const simCounts = {};
        const duplicates = [];

        // m s ln xut hin ca mi SIM
        simData.forEach((sim, index) => {
            const simNumber = sim.simNumber.trim();
            if (simNumber) {
                if (!simCounts[simNumber]) {
                    simCounts[simNumber] = [];
                }
                simCounts[simNumber].push({
                    ...sim,
                    originalIndex: index + 1
                });
            }
        });

        // Tm cc SIM trng lp
        Object.keys(simCounts).forEach(simNumber => {
            if (simCounts[simNumber].length > 1) {
                duplicates.push({
                    simNumber: simNumber,
                    count: simCounts[simNumber].length,
                    occurrences: simCounts[simNumber]
                });
            }
        });

        setDuplicateSims(duplicates);

        if (duplicates.length > 0) {
            showAlert(`Tm thy ${duplicates.length} SIM trng lp!`, 'warning');
            setShowDuplicateChecker(true);
        } else {
            showAlert('Khng c SIM trng lp!', 'success');
        }
    };

    // Xa SIM trng lp (gi li SIM u tin)
    const removeDuplicateSims = () => {
        if (duplicateSims.length === 0) return;

        const seenSims = new Set();
        const uniqueSims = simData.filter(sim => {
            const simNumber = sim.simNumber.trim();
            if (seenSims.has(simNumber)) {
                return false; // B qua SIM trng
            }
            seenSims.add(simNumber);
            return true;
        });

        setSimData(uniqueSims);
        setSimList(uniqueSims.map(sim => `${sim.simNumber}${sim.price ? ' = ' + sim.price : ''}`).join('\n'));
        setDuplicateSims([]);
        setShowDuplicateChecker(false);
        showAlert(` xa ${simData.length - uniqueSims.length} SIM trng lp!`, 'success');
    };

    // Ly config ca text c chn
    const getSelectedTextConfig = () => {
        if (selectedText === 'simNumber') {
            return textConfig.simNumber;
        } else if (selectedText === 'price') {
            return textConfig.price;
        } else if (selectedText.startsWith('custom_')) {
            const textId = parseInt(selectedText.split('_')[1]);
            return customTexts.find(text => text.id === textId) || textConfig.simNumber;
        }
        return textConfig.simNumber;
    };

    // Cp nht config ca text c chn
    const updateSelectedTextConfig = (property, value) => {
        if (selectedText === 'simNumber') {
            handleTextStyleChange('simNumber', property, value);
        } else if (selectedText === 'price') {
            handleTextStyleChange('price', property, value);
        } else if (selectedText.startsWith('custom_')) {
            const textId = parseInt(selectedText.split('_')[1]);
            updateCustomText(textId, property, value);
        }
    };

    // To nh cho mt SIM s dng HTML5 Canvas
    const generateImageForSim = (simNumber, price) => {
        return new Promise((resolve, reject) => {
            try {
                // Kim tra nh nn c sn khng
                if (!image) {
                    reject(new Error('nh nn cha c ti'));
                    return;
                }

                // To canvas vi kch thc c nh ging preview
                const canvas = document.createElement('canvas');
                canvas.width = 600; // Gi nguyn kch thc nh preview
                canvas.height = 400; // Gi nguyn kch thc nh preview
                const ctx = canvas.getContext('2d');
                
                // Ci thin cht lng render
                ctx.imageSmoothingEnabled = true;
                ctx.imageSmoothingQuality = 'high';
                
                // Thit lp DPI cao hn cho cht lng tt hn
                const dpi = window.devicePixelRatio || 1;
                if (dpi > 1) {
                    canvas.width = 600 * dpi;
                    canvas.height = 400 * dpi;
                    ctx.scale(dpi, dpi);
                }

                // To image object mi
                const img = new window.Image();
                img.crossOrigin = 'anonymous';
                
                img.onload = () => {
                    try {
                        // V nh nn vi kch thc ph hp
                        if (dpi > 1) {
                            ctx.drawImage(img, 0, 0, 600, 400);
                        } else {
                            ctx.drawImage(img, 0, 0, 600, 400);
                        }

                        // Cu hnh font cho text
                        const simFont = `${textConfig.simNumber.fontStyle} ${textConfig.simNumber.fontWeight} ${textConfig.simNumber.fontSize}px ${textConfig.simNumber.fontFamily}`;
                        const priceFont = `${textConfig.price.fontStyle} ${textConfig.price.fontWeight} ${textConfig.price.fontSize}px ${textConfig.price.fontFamily}`;

                        // V text s SIM - s dng chnh xc v tr t preview
                        ctx.font = simFont;
                        ctx.fillStyle = textConfig.simNumber.color;
                        ctx.strokeStyle = textConfig.simNumber.stroke;
                        ctx.lineWidth = textConfig.simNumber.strokeWidth;
                        ctx.shadowColor = textConfig.simNumber.shadowColor;
                        ctx.shadowBlur = textConfig.simNumber.shadowBlur;
                        ctx.shadowOffsetX = textConfig.simNumber.shadowOffset.x;
                        ctx.shadowOffsetY = textConfig.simNumber.shadowOffset.y;
                        ctx.globalAlpha = textConfig.simNumber.opacity;

                        // S dng chnh xc v tr t textConfig vi iu chnh baseline
                        const currentSimText = simNumber || '0123456789';
                        const simY = textConfig.simNumber.y + textConfig.simNumber.fontSize * 0.8; // iu chnh baseline
                        if (textConfig.simNumber.strokeWidth > 0) {
                            ctx.strokeText(currentSimText, textConfig.simNumber.x, simY);
                        }
                        ctx.fillText(currentSimText, textConfig.simNumber.x, simY);

                        // V text gi tin - s dng chnh xc v tr t preview
                        ctx.font = priceFont;
                        ctx.fillStyle = textConfig.price.color;
                        ctx.strokeStyle = textConfig.price.stroke;
                        ctx.lineWidth = textConfig.price.strokeWidth;
                        ctx.shadowColor = textConfig.price.shadowColor;
                        ctx.shadowBlur = textConfig.price.shadowBlur;
                        ctx.shadowOffsetX = textConfig.price.shadowOffset.x;
                        ctx.shadowOffsetY = textConfig.price.shadowOffset.y;
                        ctx.globalAlpha = textConfig.price.opacity;

                        // S dng chnh xc v tr t textConfig vi iu chnh baseline
                        const priceText = (price || '500000') + ' Triu';
                        const priceY = textConfig.price.y + textConfig.price.fontSize * 0.8; // iu chnh baseline
                        if (textConfig.price.strokeWidth > 0) {
                            ctx.strokeText(priceText, textConfig.price.x, priceY);
                        }
                        ctx.fillText(priceText, textConfig.price.x, priceY);

                        // V cc text ty chnh - s dng chnh xc v tr t preview
                        customTexts.forEach(customText => {
                            ctx.font = `${customText.fontStyle} ${customText.fontWeight} ${customText.fontSize}px ${customText.fontFamily}`;
                            ctx.fillStyle = customText.color;
                            ctx.strokeStyle = customText.stroke;
                            ctx.lineWidth = customText.strokeWidth;
                            ctx.shadowColor = customText.shadowColor;
                            ctx.shadowBlur = customText.shadowBlur;
                            ctx.shadowOffsetX = customText.shadowOffset.x;
                            ctx.shadowOffsetY = customText.shadowOffset.y;
                            ctx.globalAlpha = customText.opacity;

                            // S dng chnh xc v tr t customText vi iu chnh baseline
                            const customY = customText.y + customText.fontSize * 0.8; // iu chnh baseline
                            if (customText.strokeWidth > 0) {
                                ctx.strokeText(customText.content, customText.x, customY);
                            }
                            ctx.fillText(customText.content, customText.x, customY);
                        });

                        // Reset global alpha
                        ctx.globalAlpha = 1;

                        // Ly data URL
                        const dataURL = canvas.toDataURL('image/png');
                        resolve(dataURL);
                        
                    } catch (renderError) {
                        console.error('Li khi render nh:', renderError);
                        reject(renderError);
                    }
                };
                
                img.onerror = () => {
                    console.error('Li khi load nh nn');
                    reject(new Error('Khng th load nh nn'));
                };
                
                // Load nh nn
                img.src = backgroundImage;
                
            } catch (error) {
                console.error('Li khi to nh cho SIM:', simNumber, error);
                reject(error);
            }
        });
    };

    // Ti xung tng file PNG ring l
    const downloadIndividualImages = async () => {
        if (!simData.length) {
            showAlert('Vui lng nhp danh sch SIM!', 'warning');
            return;
        }

        if (!backgroundImage) {
            showAlert('Vui lng upload nh nn trc!', 'warning');
            return;
        }

        setIsGenerating(true);
        setProgress(0);
        
        try {
            let successCount = 0;
            
            for (let i = 0; i < simData.length; i++) {
                const { simNumber, price } = simData[i];
                try {
                    const imageData = await generateImageForSim(simNumber, price);
                    
                    if (imageData) {
                        // Chuyn i base64 thnh blob
                        const response = await fetch(imageData);
                        const blob = await response.blob();
                        
                        // To tn file an ton
                        const safeSimNumber = simNumber.replace(/[^a-zA-Z0-9]/g, '_');
                        const safePrice = price.replace(/[^a-zA-Z0-9]/g, '_');
                        const fileName = `sim_${safeSimNumber}_${safePrice || 'no_price'}.png`;
                        
                        // Ti xung tng file vi delay
                        saveAs(blob, fileName);
                        successCount++;
                        
                        // Delay ln hn  trnh browser block multiple downloads
                        await new Promise(resolve => setTimeout(resolve, 1000));
                    }
                } catch (error) {
                    console.error(`Li khi to nh cho SIM ${simNumber}:`, error);
                }
                
                // Cp nht progress
                setProgress(Math.round(((i + 1) / simData.length) * 100));
            }
            
            if (successCount > 0) {
                showAlert(` ti xung ${successCount}/${simData.length} file PNG thnh cng!`, 'success');
            } else {
                showAlert('Khng c file no c ti xung!', 'error');
            }
        } catch (error) {
            console.error('Li khi ti xung:', error);
            showAlert('C li xy ra khi ti xung!', 'error');
        } finally {
            setIsGenerating(false);
            setProgress(0);
        }
    };

    // Ti xung tt c nh (ZIP)
    const downloadAllImages = async () => {
        if (!simData.length) {
            showAlert('Vui lng nhp danh sch SIM!', 'warning');
            return;
        }

        if (!backgroundImage) {
            showAlert('Vui lng upload nh nn trc!', 'warning');
            return;
        }

        setIsGenerating(true);
        setProgress(0);
        try {
            const zip = new JSZip();
            
            for (let i = 0; i < simData.length; i++) {
                const { simNumber, price } = simData[i];
                try {
                    const imageData = await generateImageForSim(simNumber, price);
                    
                    if (imageData) {
                        // Chuyn i base64 thnh blob
                        const response = await fetch(imageData);
                        const blob = await response.blob();
                        
                        // Thm vo zip
                        zip.file(`sim_${simNumber}_${price}.png`, blob);
                    }
                } catch (error) {
                    console.error(`Li khi to nh cho SIM ${simNumber}:`, error);
                    // Tip tc vi SIM tip theo thay v dng li
                }
                
                // Cp nht progress
                setProgress(Math.round(((i + 1) / simData.length) * 100));
            }

            // To v ti xung zip
            const content = await zip.generateAsync({ type: 'blob' });
            saveAs(content, 'sim_images.zip');
            
            showAlert('Ti xung thnh cng!', 'success');
        } catch (error) {
            console.error('Li khi to nh:', error);
            showAlert('C li xy ra khi to nh! Vui lng th li.', 'error');
        } finally {
            setIsGenerating(false);
            setProgress(0);
        }
    };

    // Ti xung tng file mt vi xc nhn
    const downloadOneByOne = async () => {
        if (!simData.length) {
            showAlert('Vui lng nhp danh sch SIM!', 'warning');
            return;
        }

        if (!backgroundImage) {
            showAlert('Vui lng upload nh nn trc!', 'warning');
            return;
        }

        setIsGenerating(true);
        setProgress(0);
        
        try {
            for (let i = 0; i < simData.length; i++) {
                const { simNumber, price } = simData[i];
                try {
                    const imageData = await generateImageForSim(simNumber, price);
                    
                    if (imageData) {
                        // To tn file an ton
                        const safeSimNumber = simNumber.replace(/[^a-zA-Z0-9]/g, '_');
                        const safePrice = price.replace(/[^a-zA-Z0-9]/g, '_');
                        const fileName = `sim_${safeSimNumber}_${safePrice || 'no_price'}.png`;
                        
                        // To link ti xung v click t ng
                        const link = document.createElement('a');
                        link.href = imageData;
                        link.download = fileName;
                        link.style.display = 'none';
                        document.body.appendChild(link);
                        link.click();
                        document.body.removeChild(link);
                        
                        // Delay gia cc file
                        if (i < simData.length - 1) {
                            await new Promise(resolve => setTimeout(resolve, 500));
                        }
                    }
                } catch (error) {
                    console.error(`Li khi to nh cho SIM ${simNumber}:`, error);
                }
                
                // Cp nht progress
                setProgress(Math.round(((i + 1) / simData.length) * 100));
            }
            
            showAlert(` to ${simData.length} file PNG! Kim tra th mc Downloads.`, 'success');
        } catch (error) {
            console.error('Li khi ti xung:', error);
            showAlert('C li xy ra khi ti xung!', 'error');
        } finally {
            setIsGenerating(false);
            setProgress(0);
        }
    };

    // Hm ti xung chnh
    const handleDownload = () => {
        if (downloadFormat === 'zip') {
            downloadAllImages();
        } else if (downloadFormat === 'individual') {
            downloadIndividualImages();
        } else {
            downloadOneByOne();
        }
    };

    return (
        <div className="container mt-3">
            <div className="row">
                <div className="col-md-12">
                    <div className="alert alert-info mt-2">
                        <h6>Hng dn:</h6>
                        <p className="mb-0">Upload nh  Nhp SIM  Chnh text  Ti xung</p>
                    </div>
                </div>
            </div>

            <div className="row">
                {/* Panel iu khin */}
                <div className="col-md-4">
                    <div className="card">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">Ci t</h5>
                            <button 
                                className="btn btn-outline-secondary btn-sm"
                                onClick={() => {
                                    const cardBody = document.querySelector('.card-body');
                                    if (cardBody) {
                                        cardBody.scrollTo({ top: 0, behavior: 'smooth' });
                                    }
                                }}
                                title="Ln u menu"
                            >
                                
                            </button>
                        </div>
                        <div className="card-body" style={{ 
                            maxHeight: '80vh', 
                            overflowY: 'auto',
                            scrollbarWidth: 'thin',
                            scrollbarColor: '#6c757d #f8f9fa'
                        }}>
                            {/* Danh sch SIM */}
                            <div className="mb-3">
                                <label className="form-label text-primary fw-bold">Danh sch SIM:</label>
                                <textarea
                                    className="form-control"
                                    rows="4"
                                    placeholder="Dn list sim ti y..."
                                    value={simList}
                                    onChange={handleSimListChange}
                                />
                                <small className="text-muted">
                                    H tr: , = - tab
                                </small>
                                {simData.length > 0 && (
                                    <div className="mt-2">
                                        <small className="text-success">
                                              nhn din {simData.length} SIM
                                        </small>
                                    </div>
                                )}
                            </div>

                            {/* Cng c kim tra SIM trng lp */}
                            <div className="mb-3">
                                <h6 className="text-warning border-bottom pb-2"> Kim tra SIM trng lp</h6>
                                <div className="d-flex gap-2 mb-2">
                                    <button
                                        className="btn btn-warning btn-sm"
                                        onClick={checkDuplicateSims}
                                        disabled={!simData.length}
                                    >
                                        <i className="fas fa-search me-1"></i>
                                        Kim tra trng lp
                                    </button>
                                    {duplicateSims.length > 0 && (
                                        <button
                                            className="btn btn-danger btn-sm"
                                            onClick={removeDuplicateSims}
                                        >
                                            <i className="fas fa-trash me-1"></i>
                                            Xa trng lp
                                        </button>
                                    )}
                                </div>
                                {duplicateSims.length > 0 && (
                                    <div className="alert alert-warning">
                                        <strong>Tm thy {duplicateSims.length} SIM trng lp:</strong>
                                        <div className="mt-2">
                                            {duplicateSims.map((dup, index) => (
                                                <div key={index} className="mb-1">
                                                    <code>{dup.simNumber}</code> 
                                                    <span className="badge bg-danger ms-1">{dup.count} ln</span>
                                                    <small className="text-muted ms-2">
                                                        (Dng: {dup.occurrences.map(occ => occ.originalIndex).join(', ')})
                                                    </small>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                )}
                            </div>


                            {/* Add Custom Text */}
                            <div className="mb-3">
                                <h6 className="text-info border-bottom pb-2"> Thm text mi</h6>
                                <div className="input-group mb-2">
                                    <input
                                        type="text"
                                        className="form-control form-control-sm"
                                        placeholder="Nhp ni dung text..."
                                        value={newTextContent}
                                        onChange={(e) => setNewTextContent(e.target.value)}
                                        onKeyPress={(e) => e.key === 'Enter' && addCustomText()}
                                    />
                                    <button 
                                        className="btn btn-primary btn-sm"
                                        onClick={addCustomText}
                                    >
                                        Thm
                                    </button>
                                </div>
                            </div>

                            {/* Text Selection */}
                            <div className="mb-3">
                                <h6 className="text-warning border-bottom pb-2">
                                     Text ang chn: 
                                    <span className="badge bg-primary ms-2">
                                        {selectedTextId === 'simNumber' ? 'S SIM' : 
                                         selectedTextId === 'price' ? 'Gi tin' : 
                                         selectedTextId && selectedTextId.startsWith('custom_') ? 'Text ty chnh' : 
                                         'Cha chn'}
                                    </span>
                                </h6>
                                {/* <h6 className="text-warning border-bottom pb-2"> Chn text  chnh sa</h6>
                                <div className="btn-group w-100 mb-2" role="group">
                                    <button 
                                        className={`btn ${selectedText === 'simNumber' ? 'btn-primary' : 'btn-outline-primary'} btn-sm`}
                                        onClick={() => setSelectedText('simNumber')}
                                    >
                                         S SIM
                                    </button>
                                    <button 
                                        className={`btn ${selectedText === 'price' ? 'btn-success' : 'btn-outline-success'} btn-sm`}
                                        onClick={() => setSelectedText('price')}
                                    >
                                         Gi tin
                                    </button>
                                </div> */}
                                
                                {/* Custom Texts List */}
                                {customTexts.length > 0 && (
                                    <div className="mb-2">
                                        <small className="text-muted">Text ty chnh:</small>
                                        <div className="d-flex flex-wrap gap-1">
                                            {customTexts.map((customText) => (
                                                <div key={customText.id} className="d-flex align-items-center">
                                                    <button
                                                        className={`btn ${selectedText === `custom_${customText.id}` ? 'btn-info' : 'btn-outline-info'} btn-sm me-1`}
                                                        onClick={() => handleTextClick('custom', customText.id)}
                                                        style={{ fontSize: '12px' }}
                                                    >
                                                        {customText.content.length > 10 ? customText.content.substring(0, 10) + '...' : customText.content}
                                                    </button>
                                                    <button
                                                        className="btn btn-outline-danger btn-sm"
                                                        onClick={() => removeCustomText(customText.id)}
                                                        style={{ fontSize: '10px', padding: '2px 6px' }}
                                                        title="Xa text"
                                                    >
                                                        
                                                    </button>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                )}
                                
                            </div>

                            {/* Preset Styles */}
                            {/* <div className="mb-3">
                                <h6 className="text-secondary border-bottom pb-2"> Style Presets</h6>
                                <div className="btn-group w-100 mb-2" role="group">
                                    <button 
                                        className="btn btn-outline-primary btn-sm"
                                        onClick={() => {
                                            setTextConfig(prev => ({
                                                ...prev,
                                                [selectedText]: { 
                                                    ...prev[selectedText], 
                                                    color: '#000000', 
                                                    stroke: '#ffffff', 
                                                    strokeWidth: 2, 
                                                    fontWeight: 'bold' 
                                                }
                                            }));
                                        }}
                                    >
                                        Classic
                                    </button>
                                    <button 
                                        className="btn btn-outline-success btn-sm"
                                        onClick={() => {
                                            setTextConfig(prev => ({
                                                ...prev,
                                                [selectedText]: { 
                                                    ...prev[selectedText], 
                                                    color: '#ffffff', 
                                                    stroke: '#000000', 
                                                    strokeWidth: 3, 
                                                    fontWeight: 'bold' 
                                                }
                                            }));
                                        }}
                                    >
                                        Neon
                                    </button>
                                    <button 
                                        className="btn btn-outline-warning btn-sm"
                                        onClick={() => {
                                            setTextConfig(prev => ({
                                                ...prev,
                                                [selectedText]: { 
                                                    ...prev[selectedText], 
                                                    color: '#8B4513', 
                                                    stroke: '#DAA520', 
                                                    strokeWidth: 1, 
                                                    fontWeight: 'normal' 
                                                }
                                            }));
                                        }}
                                    >
                                        Vintage
                                    </button>
                                </div>
                            </div> */}

                            {/* Text Settings - Single Panel */}
                            <div className="mb-4">
                                <h6 className={
                                    selectedText === 'simNumber' ? 'text-primary' : 
                                    selectedText === 'price' ? 'text-success' : 
                                    'text-info'
                                }>
                                    {selectedText === 'simNumber' ? ' S SIM' : 
                                     selectedText === 'price' ? ' Gi tin' : 
                                     'Text ty chnh'} - Setting
                                </h6>
                                
                                {/* Font & Size */}
                                <div className="row mb-2">
                                    <div className="col-6">
                                        <label className="form-label">Font Family:</label>
                                        <select
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().fontFamily}
                                            onChange={(e) => updateSelectedTextConfig('fontFamily', e.target.value)}
                                            style={{ fontFamily: getSelectedTextConfig().fontFamily }}
                                        >
                                            {/* Sans-serif Fonts */}
                                            <optgroup label="Sans-serif Fonts">
                                                <option value="Arial">Arial</option>
                                                <option value="Helvetica">Helvetica</option>
                                                <option value="Verdana">Verdana</option>
                                                <option value="Tahoma">Tahoma</option>
                                                <option value="Trebuchet MS">Trebuchet MS</option>
                                                <option value="Calibri">Calibri</option>
                                                <option value="Segoe UI">Segoe UI</option>
                                                <option value="Open Sans">Open Sans</option>
                                                <option value="Roboto">Roboto</option>
                                                <option value="Lato">Lato</option>
                                                <option value="Source Sans Pro">Source Sans Pro</option>
                                                <option value="Poppins">Poppins</option>
                                                <option value="Montserrat">Montserrat</option>
                                                <option value="Nunito">Nunito</option>
                                                <option value="Ubuntu">Ubuntu</option>
                                            </optgroup>
                                            
                                            {/* Serif Fonts */}
                                            <optgroup label="Serif Fonts">
                                                <option value="Times New Roman">Times New Roman</option>
                                                <option value="Georgia">Georgia</option>
                                                <option value="Times">Times</option>
                                                <option value="Palatino">Palatino</option>
                                                <option value="Garamond">Garamond</option>
                                                <option value="Book Antiqua">Book Antiqua</option>
                                                <option value="Baskerville">Baskerville</option>
                                                <option value="Playfair Display">Playfair Display</option>
                                                <option value="Merriweather">Merriweather</option>
                                                <option value="Crimson Text">Crimson Text</option>
                                                <option value="Libre Baskerville">Libre Baskerville</option>
                                            </optgroup>
                                            
                                            {/* Monospace Fonts */}
                                            <optgroup label="Monospace Fonts">
                                                <option value="Courier New">Courier New</option>
                                                <option value="Courier">Courier</option>
                                                <option value="Monaco">Monaco</option>
                                                <option value="Consolas">Consolas</option>
                                                <option value="Lucida Console">Lucida Console</option>
                                                <option value="Source Code Pro">Source Code Pro</option>
                                                <option value="Fira Code">Fira Code</option>
                                                <option value="JetBrains Mono">JetBrains Mono</option>
                                            </optgroup>
                                            
                                            {/* Display Fonts */}
                                            <optgroup label="Display Fonts">
                                                <option value="Impact">Impact</option>
                                                <option value="Arial Black">Arial Black</option>
                                                <option value="Comic Sans MS">Comic Sans MS</option>
                                                <option value="Papyrus">Papyrus</option>
                                                <option value="Chalkduster">Chalkduster</option>
                                                <option value="Marker Felt">Marker Felt</option>
                                                <option value="Brush Script MT">Brush Script MT</option>
                                                <option value="Lobster">Lobster</option>
                                                <option value="Pacifico">Pacifico</option>
                                                <option value="Righteous">Righteous</option>
                                                <option value="Bangers">Bangers</option>
                                                <option value="Fredoka One">Fredoka One</option>
                                                <option value="Bungee">Bungee</option>
                                                <option value="Orbitron">Orbitron</option>
                                                <option value="Russo One">Russo One</option>
                                            </optgroup>
                                            
                                            {/* Handwriting Fonts */}
                                            <optgroup label="Handwriting Fonts">
                                                <option value="Brush Script MT">Brush Script MT</option>
                                                <option value="Lucida Handwriting">Lucida Handwriting</option>
                                                <option value="Bradley Hand">Bradley Hand</option>
                                                <option value="Kalam">Kalam</option>
                                                <option value="Caveat">Caveat</option>
                                                <option value="Dancing Script">Dancing Script</option>
                                                <option value="Indie Flower">Indie Flower</option>
                                                <option value="Permanent Marker">Permanent Marker</option>
                                                <option value="Satisfy">Satisfy</option>
                                            </optgroup>
                                            
                                            {/* Vietnamese Fonts */}
                                            <optgroup label="Vietnamese Fonts">
                                                <option value="Be Vietnam Pro">Be Vietnam Pro</option>
                                                <option value="Inter">Inter</option>
                                                <option value="Lexend">Lexend</option>
                                                <option value="Work Sans">Work Sans</option>
                                                <option value="Quicksand">Quicksand</option>
                                                <option value="Comfortaa">Comfortaa</option>
                                                <option value="Varela Round">Varela Round</option>
                                                <option value="M PLUS Rounded 1c">M PLUS Rounded 1c</option>
                                            </optgroup>
                                        </select>
                                        
                                        <div className="mt-2">
                                            <div 
                                                className="border rounded p-2 bg-light"
                                                style={{ 
                                                    fontFamily: getSelectedTextConfig().fontFamily,
                                                    fontSize: '14px',
                                                    minHeight: '30px',
                                                    display: 'flex',
                                                    alignItems: 'center'
                                                }}
                                            >
                                                {getSelectedTextConfig().fontFamily} - Sample Text
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-6">
                                        <label className="form-label">Font Size:</label>
                                        <input
                                            type="number"
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().fontSize}
                                            onChange={(e) => updateSelectedTextConfig('fontSize', parseInt(e.target.value))}
                                            min="8"
                                            max="72"
                                        />
                                    </div>
                                </div>

                                {/* Style & Weight */}
                                <div className="row mb-2">
                                    <div className="col-6">
                                        <label className="form-label">Style:</label>
                                        <select
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().fontStyle}
                                            onChange={(e) => updateSelectedTextConfig('fontStyle', e.target.value)}
                                        >
                                            <option value="normal">Normal</option>
                                            <option value="italic">Italic</option>
                                        </select>
                                    </div>
                                    <div className="col-6">
                                        <label className="form-label">Weight:</label>
                                        <select
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().fontWeight}
                                            onChange={(e) => updateSelectedTextConfig('fontWeight', e.target.value)}
                                        >
                                            <option value="normal">Normal</option>
                                            <option value="bold">Bold</option>
                                            <option value="bolder">Bolder</option>
                                        </select>
                                    </div>
                                </div>

                                {/* Colors */}
                                <div className="row mb-2">
                                    <div className="col-6">
                                        <label className="form-label">Mu ch:</label>
                                        <input
                                            type="color"
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().color}
                                            onChange={(e) => updateSelectedTextConfig('color', e.target.value)}
                                        />
                                    </div>
                                    <div className="col-6">
                                        <label className="form-label">Mu vin:</label>
                                        <input
                                            type="color"
                                            className="form-control form-control-sm"
                                            value={getSelectedTextConfig().stroke}
                                            onChange={(e) => updateSelectedTextConfig('stroke', e.target.value)}
                                        />
                                    </div>
                                </div>

                                {/* Stroke Width & Opacity */}
                                <div className="row mb-2">
                                    <div className="col-6">
                                        <label className="form-label"> dy vin:</label>
                                        <input
                                            type="range"
                                            className="form-range"
                                            min="0"
                                            max="5"
                                            value={getSelectedTextConfig().strokeWidth}
                                            onChange={(e) => updateSelectedTextConfig('strokeWidth', parseInt(e.target.value))}
                                        />
                                        <small className="text-muted">{getSelectedTextConfig().strokeWidth}px</small>
                                    </div>
                                    <div className="col-6">
                                        <label className="form-label"> m:</label>
                                        <input
                                            type="range"
                                            className="form-range"
                                            min="0.1"
                                            max="1"
                                            step="0.1"
                                            value={getSelectedTextConfig().opacity}
                                            onChange={(e) => updateSelectedTextConfig('opacity', parseFloat(e.target.value))}
                                        />
                                        <small className="text-muted">{Math.round(getSelectedTextConfig().opacity * 100)}%</small>
                                    </div>
                                </div>

                                {/* Shadow */}
                                <div className="mb-2">
                                    <label className="form-label">Bng :</label>
                                    <div className="row">
                                        <div className="col-4">
                                            <label className="form-label small">Mu:</label>
                                            <input
                                                type="color"
                                                className="form-control form-control-sm"
                                                value={getSelectedTextConfig().shadowColor}
                                                onChange={(e) => updateSelectedTextConfig('shadowColor', e.target.value)}
                                            />
                                        </div>
                                        <div className="col-4">
                                            <label className="form-label small">M:</label>
                                            <input
                                                type="range"
                                                className="form-range"
                                                min="0"
                                                max="10"
                                                value={getSelectedTextConfig().shadowBlur}
                                                onChange={(e) => updateSelectedTextConfig('shadowBlur', parseInt(e.target.value))}
                                            />
                                        </div>
                                        <div className="col-4">
                                            <label className="form-label small">X:</label>
                                            <input
                                                type="range"
                                                className="form-range"
                                                min="-5"
                                                max="5"
                                                value={getSelectedTextConfig().shadowOffset.x}
                                                onChange={(e) => updateSelectedTextConfig('shadowOffset', { ...getSelectedTextConfig().shadowOffset, x: parseInt(e.target.value) })}
                                            />
                                        </div>
                                    </div>
                                </div>
                            </div>

                            {/* Progress bar */}
                            {isGenerating && (
                                <div className="mb-3">
                                    <div className="progress">
                                        <div 
                                            className="progress-bar" 
                                            role="progressbar" 
                                            style={{ width: `${progress}%` }}
                                            aria-valuenow={progress} 
                                            aria-valuemin="0" 
                                            aria-valuemax="100"
                                        >
                                            {progress}%
                                        </div>
                                    </div>
                                </div>
                            )}

                            {/* Cn gia th cng */}
                            <div className="mb-3">
                                <h6 className="text-info border-bottom pb-2"> Cn Chnh Text</h6>
                                <button
                                    className="btn btn-primary btn-sm w-100"
                                    onClick={() => {
                                        if (selectedTextId === 'simNumber') {
                                            const textContent = simData.length > 0 ? simData[0].simNumber : "0123456789";
                                            const { width: textWidth } = calculateTextSize(textConfig.simNumber, textContent);
                                            const centerX = 300;
                                            const newX = centerX - textWidth / 2;
                                            
                                            setTextConfig(prev => ({
                                                ...prev,
                                                simNumber: {
                                                    ...prev.simNumber,
                                                    x: Math.round(newX)
                                                }
                                            }));
                                        } else if (selectedTextId === 'price') {
                                            const textContent = simData.length > 0 ? simData[0].price + " Triu" : "500,000 VN";
                                            const { width: textWidth } = calculateTextSize(textConfig.price, textContent);
                                            const centerX = 300;
                                            const newX = centerX - textWidth / 2;
                                            
                                            setTextConfig(prev => ({
                                                ...prev,
                                                price: {
                                                    ...prev.price,
                                                    x: Math.round(newX)
                                                }
                                            }));
                                        } else if (selectedTextId && selectedTextId.startsWith('custom_')) {
                                            const textId = parseInt(selectedTextId.split('_')[1]);
                                            const customText = customTexts.find(text => text.id === textId);
                                            if (customText) {
                                                const { width: textWidth } = calculateTextSize(customText, customText.content);
                                                const centerX = 300;
                                                const newX = centerX - textWidth / 2;
                                                
                                                setCustomTexts(prev => prev.map(text => 
                                                    text.id === textId ? { 
                                                        ...text, 
                                                        x: Math.round(newX)
                                                    } : text
                                                ));
                                                showAlert(' cn gia text ty chnh!', 'success');
                                            }
                                        } else {
                                            showAlert('Vui lng chn text  cn gia!', 'warning');
                                        }
                                    }}
                                    disabled={!selectedTextId}
                                >
                                    <i className="fas fa-align-center me-1"></i>
                                    Cn gia text c chn
                                </button>
                            </div>

                            {/* Cht lng nh */}
                            <div className="mb-3">
                                <h6 className="text-success border-bottom pb-2"> Cht Lng nh</h6>
                                <select
                                    className="form-control form-control-sm"
                                    value={imageQuality}
                                    onChange={(e) => setImageQuality(parseInt(e.target.value))}
                                >
                                    <option value={1}>Thng (600x400px)</option>
                                    <option value={2}>Cao (1200x800px)</option>
                                    <option value={3}>Rt cao (1800x1200px)</option>
                                    <option value={4}>Siu cao (2400x1600px)</option>
                                </select>
                            </div>

                            {/* nh dng ti xung */}
                            <div className="mb-3">
                                <h6 className="text-dark border-bottom pb-2"> Ti xung</h6>
                                <div className="btn-group-vertical w-100" role="group">
                                    <button 
                                        className={`btn ${downloadFormat === 'zip' ? 'btn-primary' : 'btn-outline-primary'} btn-sm mb-1`}
                                        onClick={() => setDownloadFormat('zip')}
                                    >
                                         ZIP
                                    </button>
                                    <button 
                                        className={`btn ${downloadFormat === 'onebyone' ? 'btn-success' : 'btn-outline-success'} btn-sm mb-1`}
                                        onClick={() => setDownloadFormat('onebyone')}
                                    >
                                         Tng file
                                    </button>
                                    <button 
                                        className={`btn ${downloadFormat === 'individual' ? 'btn-warning' : 'btn-outline-warning'} btn-sm`}
                                        onClick={() => setDownloadFormat('individual')}
                                    >
                                         Tt c
                                    </button>
                                </div>
                            </div>

                            {/* Nt ti xung */}
                            <button
                                className="btn btn-success w-100"
                                onClick={handleDownload}
                                disabled={!backgroundImage || !simData.length || isGenerating}
                            >
                                {isGenerating ? `ang to... ${progress}%` : 
                                 downloadFormat === 'zip' ? ` ZIP (${simData.length})` : 
                                 downloadFormat === 'onebyone' ? ` Tng file (${simData.length})` :
                                 ` Tt c (${simData.length})`}
                            </button>
                        </div>
                    </div>
                </div>

                {/* Canvas preview */}
                <div className="col-md-8">
                    <div className="card">
                        <div className="card-header d-flex justify-content-between align-items-center">
                            <h5 className="mb-0">Preview</h5>
                            <div className="d-flex gap-2">
                                {backgroundImage && (
                                    <button
                                        type="button"
                                        className={`btn btn-sm ${showSnapGuides ? 'btn-primary' : 'btn-outline-primary'}`}
                                        onClick={() => setShowSnapGuides(!showSnapGuides)}
                                        title={showSnapGuides ? 'n li cn chnh' : 'Hin li cn chnh'}
                                    >
                                        <i className="fas fa-th me-1"></i>
                                        {showSnapGuides ? 'n li' : 'Hin li'}
                                    </button>
                                )}
                                {backgroundImage && (
                                    <button
                                        type="button"
                                        className="btn btn-sm btn-outline-primary"
                                        onClick={handlePreviewClick}
                                        title="Thay i nh"
                                    >
                                        <i className="fas fa-image me-1"></i>
                                        Thay i nh
                                    </button>
                                )}
                            </div>
                        </div>
                        <div className="card-body">
                            {/* Hidden file input */}
                            <input
                                ref={fileInputRef}
                                type="file"
                                accept="image/*"
                                onChange={handleFileInputChange}
                                style={{ display: 'none' }}
                            />
                            
                            <div 
                                className={`preview-drop-zone ${isDragOverPreview ? 'drag-over' : ''}`}
                                onDragOver={handlePreviewDragOver}
                                onDragLeave={handlePreviewDragLeave}
                                onDrop={handlePreviewDrop}
                                onClick={handlePreviewClick}
                                style={{
                                    position: 'relative',
                                    minHeight: '400px',
                                    border: isDragOverPreview ? '3px dashed #007bff' : '1px solid #ccc',
                                    borderRadius: '8px',
                                    backgroundColor: isDragOverPreview ? '#e3f2fd' : 'transparent',
                                    transition: 'all 0.3s ease',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    cursor: 'pointer'
                                }}
                            >
                                {backgroundImage ? (
                                    <div 
                                        style={{ border: '1px solid #ccc', display: 'inline-block' }} 
                                        className='img-thumbnail'
                                        onDragOver={(e) => {
                                            e.preventDefault();
                                            e.dataTransfer.dropEffect = 'copy';
                                        }}
                                        onDrop={(e) => {
                                            e.preventDefault();
                                            try {
                                                const imageData = JSON.parse(e.dataTransfer.getData('application/json'));
                                                if (imageData.type === 'image') {
                                                    handlePreviewDragToCanvas(imageData);
                                                }
                                            } catch (error) {
                                                console.error('Error handling drop:', error);
                                            }
                                        }}
                                        onClick={(e) => {
                                            e.stopPropagation(); // Prevent opening file dialog when clicking on canvas
                                        }}
                                    >
                                    <Stage
                                        ref={stageRef}
                                        width={600}
                                        height={400}
                                        onClick={(e) => {
                                            // B chn text khi click vo canvas trng
                                            if (e.target === e.target.getStage()) {
                                                setSelectedTextId(null);
                                                setSelectedText('simNumber');
                                            }
                                        }}
                                    >
                                        <Layer>
                                            <Image
                                                image={image}
                                                width={600}
                                                height={400}
                                            />
                                            {/* Snap Guide Lines */}
                                            {showSnapGuides && snapEnabled && (
                                                <>
                                                    {/* 3x3 Grid Lines */}
                                                    {/* Vertical grid lines */}
                                                    <Line
                                                        points={[200, 0, 200, 400]}
                                                        stroke="#007bff"
                                                        strokeWidth={1}
                                                        dash={[4, 4]}
                                                        opacity={0.6}
                                                    />
                                                    <Line
                                                        points={[400, 0, 400, 400]}
                                                        stroke="#007bff"
                                                        strokeWidth={1}
                                                        dash={[4, 4]}
                                                        opacity={0.6}
                                                    />
                                                    {/* Horizontal grid lines */}
                                                    <Line
                                                        points={[0, 133.33, 600, 133.33]}
                                                        stroke="#007bff"
                                                        strokeWidth={1}
                                                        dash={[4, 4]}
                                                        opacity={0.6}
                                                    />
                                                    <Line
                                                        points={[0, 266.67, 600, 266.67]}
                                                        stroke="#007bff"
                                                        strokeWidth={1}
                                                        dash={[4, 4]}
                                                        opacity={0.6}
                                                    />
                                                    
                                                    {/* Center lines (highlighted) */}
                                                    <Line
                                                        points={[300, 0, 300, 400]}
                                                        stroke="#ff0000"
                                                        strokeWidth={2}
                                                        dash={[5, 5]}
                                                        opacity={0.8}
                                                    />
                                                    <Line
                                                        points={[0, 200, 600, 200]}
                                                        stroke="#ff0000"
                                                        strokeWidth={2}
                                                        dash={[5, 5]}
                                                        opacity={0.8}
                                                    />
                                                    
                                                    {/* Edge lines */}
                                                    <Line
                                                        points={[0, 0, 0, 400]}
                                                        stroke="#00ff00"
                                                        strokeWidth={1}
                                                        dash={[3, 3]}
                                                        opacity={0.4}
                                                    />
                                                    <Line
                                                        points={[600, 0, 600, 400]}
                                                        stroke="#00ff00"
                                                        strokeWidth={1}
                                                        dash={[3, 3]}
                                                        opacity={0.4}
                                                    />
                                                    <Line
                                                        points={[0, 0, 600, 0]}
                                                        stroke="#00ff00"
                                                        strokeWidth={1}
                                                        dash={[3, 3]}
                                                        opacity={0.4}
                                                    />
                                                    <Line
                                                        points={[0, 400, 600, 400]}
                                                        stroke="#00ff00"
                                                        strokeWidth={1}
                                                        dash={[3, 3]}
                                                        opacity={0.4}
                                                    />
                                                </>
                                            )}
                                            {/* Selection Border */}
                                            {selectedTextId === 'simNumber' && (
                                                <Rect
                                                    {...createSelectionBorder(textConfig.simNumber, simData.length > 0 ? simData[0].simNumber : "0123456789")}
                                                />
                                            )}
                                            {selectedTextId === 'price' && (
                                                <Rect
                                                    {...createSelectionBorder(textConfig.price, simData.length > 0 ? simData[0].price + " Triu" : "500,000 VN")}
                                                />
                                            )}
                                            {selectedTextId && selectedTextId.startsWith('custom_') && (
                                                (() => {
                                                    const textId = parseInt(selectedTextId.split('_')[1]);
                                                    const customText = customTexts.find(text => text.id === textId);
                                                    if (customText) {
                                                        return (
                                                            <Rect
                                                                {...createSelectionBorder(customText, customText.content)}
                                                            />
                                                        );
                                                    }
                                                    return null;
                                                })()
                                            )}
                                            <Text
                                                name="simNumber"
                                                text={simData.length > 0 ? simData[0].simNumber : "0123456789"}
                                                x={textConfig.simNumber.x}
                                                y={textConfig.simNumber.y}
                                                fontSize={textConfig.simNumber.fontSize}
                                                fill={textConfig.simNumber.color}
                                                fontFamily={textConfig.simNumber.fontFamily}
                                                fontStyle={textConfig.simNumber.fontStyle}
                                                fontWeight={textConfig.simNumber.fontWeight}
                                                stroke={selectedText === 'simNumber' ? '#007bff' : textConfig.simNumber.stroke}
                                                strokeWidth={selectedText === 'simNumber' ? 3 : textConfig.simNumber.strokeWidth}
                                                shadowColor={textConfig.simNumber.shadowColor}
                                                shadowBlur={textConfig.simNumber.shadowBlur}
                                                shadowOffset={textConfig.simNumber.shadowOffset}
                                                opacity={textConfig.simNumber.opacity}
                                                draggable
                                                onClick={() => handleTextClick('simNumber')}
                                                onTap={() => handleTextClick('simNumber')}
                                                onDragEnd={(e) => handleTextDrag('simNumber', e.target.position())}
                                                onDragMove={(e) => {
                                                    // Cp nht real-time khi ko
                                                    const pos = e.target.position();
                                                    setTextConfig(prev => ({
                                                        ...prev,
                                                        simNumber: {
                                                            ...prev.simNumber,
                                                            x: Math.round(pos.x),
                                                            y: Math.round(pos.y)
                                                        }
                                                    }));
                                                }}
                                            />
                                            <Text
                                                name="price"
                                                text={simData.length > 0 ? simData[0].price + " Triu" : "500,000 VN"}
                                                x={textConfig.price.x}
                                                y={textConfig.price.y}
                                                fontSize={textConfig.price.fontSize}
                                                fill={textConfig.price.color}
                                                fontFamily={textConfig.price.fontFamily}
                                                fontStyle={textConfig.price.fontStyle}
                                                fontWeight={textConfig.price.fontWeight}
                                                stroke={selectedText === 'price' ? '#007bff' : textConfig.price.stroke}
                                                strokeWidth={selectedText === 'price' ? 3 : textConfig.price.strokeWidth}
                                                shadowColor={textConfig.price.shadowColor}
                                                shadowBlur={textConfig.price.shadowBlur}
                                                shadowOffset={textConfig.price.shadowOffset}
                                                opacity={textConfig.price.opacity}
                                                draggable
                                                onClick={() => handleTextClick('price')}
                                                onTap={() => handleTextClick('price')}
                                                onDragEnd={(e) => handleTextDrag('price', e.target.position())}
                                                onDragMove={(e) => {
                                                    // Cp nht real-time khi ko
                                                    const pos = e.target.position();
                                                    setTextConfig(prev => ({
                                                        ...prev,
                                                        price: {
                                                            ...prev.price,
                                                            x: Math.round(pos.x),
                                                            y: Math.round(pos.y)
                                                        }
                                                    }));
                                                }}
                                            />
                                            {/* Custom Texts */}
                                            {customTexts.map((customText, index) => (
                                                <Text
                                                    key={customText.id}
                                                    name={`custom_${customText.id}`}
                                                    text={customText.content}
                                                    x={customText.x}
                                                    y={customText.y}
                                                    fontSize={customText.fontSize}
                                                    fill={customText.color}
                                                    fontFamily={customText.fontFamily}
                                                    fontStyle={customText.fontStyle}
                                                    fontWeight={customText.fontWeight}
                                                    stroke={selectedText === `custom_${customText.id}` ? '#007bff' : customText.stroke}
                                                    strokeWidth={selectedText === `custom_${customText.id}` ? 3 : customText.strokeWidth}
                                                    shadowColor={customText.shadowColor}
                                                    shadowBlur={customText.shadowBlur}
                                                    shadowOffset={customText.shadowOffset}
                                                    opacity={customText.opacity}
                                                    draggable
                                                    onClick={() => handleTextClick('custom', customText.id)}
                                                    onTap={() => handleTextClick('custom', customText.id)}
                                                    onDragEnd={(e) => {
                                                        const pos = e.target.position();
                                                        updateCustomTextPosition(customText.id, pos);
                                                    }}
                                                    onDragMove={(e) => {
                                                        const pos = e.target.position();
                                                        setCustomTexts(prev => prev.map(text => 
                                                            text.id === customText.id ? { 
                                                                ...text, 
                                                                x: Math.round(pos.x), 
                                                                y: Math.round(pos.y) 
                                                            } : text
                                                        ));
                                                    }}
                                                />
                                            ))}
                                        </Layer>
                                    </Stage>
                                </div>
                                ) : (
                                    <div className="text-center p-5" style={{ 
                                        border: isDragOverPreview ? '3px dashed #007bff' : '2px dashed #ccc',
                                        backgroundColor: isDragOverPreview ? '#e3f2fd' : '#f8f9fa',
                                        borderRadius: '8px',
                                        transition: 'all 0.3s ease',
                                        pointerEvents: 'none' // Prevent click on placeholder
                                    }}>
                                        <div style={{ fontSize: '3rem', marginBottom: '1rem' }}>
                                            {isDragOverPreview ? '' : ''}
                                        </div>
                                        <h5 className="mb-2">
                                            {isDragOverPreview ? 'Th nh vo y' : 'Click hoc ko th nh vo y'}
                                        </h5>
                                        <p className="text-muted mb-3">
                                            Chn nh t my tnh  bt u to SIM
                                        </p>
                                        <small className="text-info">
                                            H tr: JPG, PNG, GIF, WebP (Ti a 10MB)
                                        </small>
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Hin th danh sch SIM  parse */}
            {simData.length > 0 && (
                <div className="row mt-4">
                    <div className="col-md-12">
                        <div className="card">
                            <div className="card-header">
                                <h5>Danh sch SIM ({simData.length} SIM)</h5>
                            </div>
                            <div className="card-body">
                                <div className="table-responsive">
                                    <table className="table table-striped">
                                        <thead>
                                            <tr>
                                                <th>STT</th>
                                                <th>S SIM</th>
                                                <th>Gi tin</th>
                                                <th>Du phn cch</th>
                                                <th>Trng thi</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {simData.map((sim, index) => (
                                                <tr key={index}>
                                                    <td>{index + 1}</td>
                                                    <td>
                                                        <code>{sim.simNumber}</code>
                                                    </td>
                                                    <td>
                                                        <code>{sim.price || 'Khng c'}</code>
                                                    </td>
                                                    <td>
                                                        <span className="badge bg-secondary">
                                                            {sim.separator || 'Khng c'}
                                                        </span>
                                                    </td>
                                                    <td>
                                                        {sim.simNumber && sim.price ? 
                                                            <span className="badge bg-success"> OK</span> :
                                                            sim.simNumber ? 
                                                            <span className="badge bg-warning"> Ch c s SIM</span> :
                                                            <span className="badge bg-danger"> Li</span>
                                                        }
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal chi tit SIM trng lp */}
            {showDuplicateChecker && duplicateSims.length > 0 && (
                <div className="modal fade show" style={{ display: 'block' }} tabIndex="-1">
                    <div className="modal-dialog modal-lg">
                        <div className="modal-content">
                            <div className="modal-header bg-warning">
                                <h5 className="modal-title">
                                    <i className="fas fa-exclamation-triangle me-2"></i>
                                    SIM Trng Lp
                                </h5>
                                <button
                                    type="button"
                                    className="btn-close"
                                    onClick={() => setShowDuplicateChecker(false)}
                                ></button>
                            </div>
                            <div className="modal-body">
                                <div className="alert alert-info">
                                    <strong>Tm thy {duplicateSims.length} SIM b trng lp trong danh sch!</strong>
                                    <br />
                                    <small>Bn c th xa cc SIM trng lp  gi li ch SIM u tin ca mi s.</small>
                                </div>
                                
                                <div className="table-responsive">
                                    <table className="table table-striped">
                                        <thead>
                                            <tr>
                                                <th>S SIM</th>
                                                <th>S ln xut hin</th>
                                                <th>V tr trong danh sch</th>
                                                <th>Gi tin</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {duplicateSims.map((dup, index) => (
                                                <tr key={index}>
                                                    <td>
                                                        <code className="text-primary">{dup.simNumber}</code>
                                                    </td>
                                                    <td>
                                                        <span className="badge bg-danger">{dup.count} ln</span>
                                                    </td>
                                                    <td>
                                                        <small className="text-muted">
                                                            Dng: {dup.occurrences.map(occ => occ.originalIndex).join(', ')}
                                                        </small>
                                                    </td>
                                                    <td>
                                                        {dup.occurrences.map((occ, occIndex) => (
                                                            <div key={occIndex} className="small">
                                                                {occ.price || 'Khng c'} 
                                                                {occIndex < dup.occurrences.length - 1 && <br />}
                                                            </div>
                                                        ))}
                                                    </td>
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button
                                    type="button"
                                    className="btn btn-secondary"
                                    onClick={() => setShowDuplicateChecker(false)}
                                >
                                    ng
                                </button>
                                <button
                                    type="button"
                                    className="btn btn-danger"
                                    onClick={removeDuplicateSims}
                                >
                                    <i className="fas fa-trash me-1"></i>
                                    Xa tt c SIM trng lp
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            )}

            {/* Modal backdrop */}
            {showDuplicateChecker && (
                <div className="modal-backdrop fade show"></div>
            )}
        </div>
    );
};

export default SimImageGenerator;
