import React, { useState } from "react";
import {
    MainContainer,
    MessageContainer,
    MessageHeader,
    MessageInput,
    MessageList,
    MinChatUiProvider
} from "@minchat/react-chat-ui";
import OpenAIService from "../service/OpenAIService";

function Chat() {
    const [messages, setMessages] = useState([
        {
            text: "Xin cho! Ti l AI, bn cn ti gip g?",
            user: { id: "ai", name: "AI Assistant" },
        }
    ]);
    const [isLoading, setIsLoading] = useState(false);
    const openAIService = OpenAIService();

    const currentUserId = "user";

    const handleSend = async (text) => {
        if (!text.trim() || isLoading) return;

        const userMessage = {
            text: text,
            user: { id: currentUserId, name: "Bn" }
        };
        setMessages(prev => [...prev, userMessage]);
        setIsLoading(true);

        try {
            const aiReply = await openAIService.sendMessage(text);
            
            const aiMessage = {
                text: aiReply,
                user: { id: "ai", name: "AI Assistant" }
            };
            setMessages(prev => [...prev, aiMessage]);
        } catch (err) {
            console.error("Li khi gi tin nhn:", err);
            // X l li nu cn
        } finally {
            setIsLoading(false);
        }
    };

    return (
        <div className="chat-container" style={{ height: "85vh" }}>
            <MinChatUiProvider theme="#70a7ffff">
                <MainContainer>
                    <MessageContainer>
                        <MessageHeader />
                        <MessageList
                            currentUserId={currentUserId}
                            messages={messages}
                            // loading={isLoading}
                        />
                        <MessageInput
                            placeholder="Nhp tin nhn..."
                            onSendMessage={handleSend}
                            disabled={isLoading}
                        />
                    </MessageContainer>
                </MainContainer>
            </MinChatUiProvider>
        </div>
    );
}

export default Chat;