import React, { useState } from 'react';
import {
    MDBBtn,
    MDBContainer,
    MDBRow,
    MDBCol,
    MDBCard,
    MDBCardBody,
    MDBCardHeader,
    MDBInput,
    MDBIcon,
    MDBSpinner
} from 'mdb-react-ui-kit';
import { Link, useNavigate } from 'react-router-dom';
import { showAlert } from '../service/AlertServices';
import { checkUsernameExists, saveAccount } from '../api/Accounts';
import bcrypt from 'bcryptjs';
import AvatarUpload from './AvatarUpload';

function SaveAccount() {
    const [formData, setFormData] = useState({
        fullname: '',
        username: '',
        password: '',
        confirmPassword: '',
        role: 'USER'
    });
    const [avatar, setAvatar] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [errors, setErrors] = useState({});
    const [showPassword, setShowPassword] = useState(false);
    const [showConfirmPassword, setShowConfirmPassword] = useState(false);
    const nav = useNavigate();

    // X l thay i input
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({
            ...prev,
            [name]: value
        }));
        
        // Clear error when user starts typing
        if (errors[name]) {
            setErrors(prev => ({
                ...prev,
                [name]: ''
            }));
        }
    };

    // X l avatar change
    const handleAvatarChange = (avatarUrl, publicId) => {
        setAvatar(publicId);
    };

    // Validation form
    const validateForm = () => {
        const newErrors = {};
        
        if (!formData.fullname.trim()) {
            newErrors.fullname = 'Vui lng nhp h v tn';
        } else if (formData.fullname.trim().length < 2) {
            newErrors.fullname = 'H tn phi c t nht 2 k t';
        }
        
        if (!formData.username.trim()) {
            newErrors.username = 'Vui lng nhp tn ng nhp';
        } else if (formData.username.trim().length < 3) {
            newErrors.username = 'Tn ng nhp phi c t nht 3 k t';
        } else if (!/^[a-zA-Z0-9_]+$/.test(formData.username)) {
            newErrors.username = 'Tn ng nhp ch c cha ch ci, s v du gch di';
        }
        
        if (!formData.password) {
            newErrors.password = 'Vui lng nhp mt khu';
        } 
        if (!formData.confirmPassword) {
            newErrors.confirmPassword = 'Vui lng xc nhn mt khu';
        } else if (formData.password !== formData.confirmPassword) {
            newErrors.confirmPassword = 'Mt khu xc nhn khng khp';
        }
        
        setErrors(newErrors);
        return Object.keys(newErrors).length === 0;
    };

    // X l submit form
    const handleSubmit = async (e) => {
        e.preventDefault();
        
        if (!validateForm()) {
            return;
        }

        setIsLoading(true);
        
        try {
            // Kim tra username  tn ti cha
            const usernameExists = await checkUsernameExists(formData.username);
            if (usernameExists) {
                setErrors({ username: 'Tn ng nhp  tn ti, vui lng chn tn khc!' });
                return;
            }

            // Hash password
            const hashedPassword = await bcrypt.hash(formData.password, 10);
            
            // To account data
            const accountData = {
                fullname: formData.fullname.trim(),
                username: formData.username.trim(),
                password: hashedPassword,
                role: formData.role,
                status: true,
                isApprove: false, // Ti khon mi cn c admin duyt
                avatar: avatar
            };

            // Lu account
            await saveAccount(accountData);
            
            showAlert("ng k ti khon thnh cng! Ti khon ca bn ang ch admin duyt. Bn s nhn c thng bo khi c ph duyt.", "success");
            nav('/login');
            
        } catch (error) {
            console.error("Li khi to ti khon:", error);
            showAlert("C li xy ra khi to ti khon. Vui lng th li!", "error");
        } finally {
            setIsLoading(false);
        }
    };

    // Toggle password visibility
    const togglePasswordVisibility = (field) => {
        if (field === 'password') {
            setShowPassword(!showPassword);
        } else {
            setShowConfirmPassword(!showConfirmPassword);
        }
    };

    return (
        <div className="min-vh-100 d-flex align-items-center" style={{ 
            background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
            padding: '20px 0'
        }}>
            <MDBContainer fluid className="px-3">
                {/* Header vi nt v home */}
                <div className="text-center mb-4">
                    <Link to="/" className="text-decoration-none">
                        <MDBBtn
                            color="light"
                            className="d-inline-flex align-items-center shadow-sm"
                            style={{
                                borderRadius: '25px',
                                padding: '10px 25px',
                                fontWeight: '600',
                                transition: 'all 0.3s ease',
                                border: 'none'
                            }}
                        >
                            <MDBIcon fas icon="home" className="me-2" />
                            V Trang Ch
                        </MDBBtn>
                    </Link>
                </div>

                <MDBRow className="justify-content-center">
                    <MDBCol xs="12" sm="10" md="8" lg="6" xl="5">
                        <MDBCard className="shadow-lg" style={{ borderRadius: '20px', overflow: 'hidden' }}>
                            {/* Card Header */}
                            <MDBCardHeader 
                                className="text-center py-4" 
                                style={{ 
                                    background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
                                    border: 'none'
                                }}
                            >
                                <MDBIcon icon="user-plus" size="3x" className="text-white mb-3" />
                                <h2 className="text-white mb-0 fw-bold">ng K Ti Khon</h2>
                                <p className="text-white-50 mb-0">To ti khon mi  bt u s dng</p>
                            </MDBCardHeader>

                            <MDBCardBody className="p-4 p-md-5">
                                {/* Form */}
                                <form onSubmit={handleSubmit}>
                                    {/* Avatar Upload Section */}
                                    <div className="text-center mb-4">
                                        <AvatarUpload
                                            currentAvatar={avatar}
                                            onAvatarChange={handleAvatarChange}
                                            size={80}
                                        />
                                        <p className="text-muted small mt-2">
                                            <MDBIcon icon="camera" className="me-1" />
                                            Ti ln nh i din (ty chn)
                                        </p>
                                    </div>

                                    {/* Full Name Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="user" className="me-2 text-primary" />
                                            H v tn *
                                        </label>
                                        <MDBInput
                                            wrapperClass='mb-2'
                                            id='fullname'
                                            name='fullname'
                                            type='text'
                                            value={formData.fullname}
                                            onChange={handleInputChange}
                                            placeholder="Nhp h v tn ca bn..."
                                            className={errors.fullname ? 'is-invalid' : ''}
                                        />
                                        {errors.fullname && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.fullname}
                                            </div>
                                        )}
                                    </div>

                                    {/* Username Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="at" className="me-2 text-primary" />
                                            Tn ng nhp *
                                        </label>
                                        <MDBInput
                                            wrapperClass='mb-2'
                                            id='username'
                                            name='username'
                                            type='text'
                                            value={formData.username}
                                            onChange={handleInputChange}
                                            placeholder="Nhp tn ng nhp..."
                                            className={errors.username ? 'is-invalid' : ''}
                                        />
                                        {errors.username && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.username}
                                            </div>
                                        )}
                                        <small className="text-muted">
                                            Ch c cha ch ci, s v du gch di
                                        </small>
                                    </div>

                                    {/* Password Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="lock" className="me-2 text-primary" />
                                            Mt khu *
                                        </label>
                                        <div className="position-relative">
                                            <MDBInput
                                                wrapperClass='mb-2'
                                                id='password'
                                                name='password'
                                                type={showPassword ? 'text' : 'password'}
                                                value={formData.password}
                                                onChange={handleInputChange}
                                                placeholder="Nhp mt khu..."
                                                className={errors.password ? 'is-invalid' : ''}
                                            />
                                            <button
                                                type="button"
                                                className="btn btn-link position-absolute"
                                                style={{
                                                    right: '15px',
                                                    top: '50%',
                                                    transform: 'translateY(-50%)',
                                                    border: 'none',
                                                    background: 'none',
                                                    padding: '0',
                                                    zIndex: '10',
                                                    color: '#6c757d'
                                                }}
                                                onClick={() => togglePasswordVisibility('password')}
                                            >
                                                <MDBIcon 
                                                    icon={showPassword ? 'eye-slash' : 'eye'} 
                                                    size="lg"
                                                />
                                            </button>
                                        </div>
                                        {errors.password && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.password}
                                            </div>
                                        )}
                                        <small className="text-muted">
                                            t nht 6 k t, bao gm ch hoa, ch thng v s
                                        </small>
                                    </div>

                                    {/* Confirm Password Field */}
                                    <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="lock" className="me-2 text-primary" />
                                            Xc nhn mt khu *
                                        </label>
                                        <div className="position-relative">
                                            <MDBInput
                                                wrapperClass='mb-2'
                                                id='confirmPassword'
                                                name='confirmPassword'
                                                type={showConfirmPassword ? 'text' : 'password'}
                                                value={formData.confirmPassword}
                                                onChange={handleInputChange}
                                                placeholder="Nhp li mt khu..."
                                                className={errors.confirmPassword ? 'is-invalid' : ''}
                                            />
                                            <button
                                                type="button"
                                                className="btn btn-link position-absolute"
                                                style={{
                                                    right: '15px',
                                                    top: '50%',
                                                    transform: 'translateY(-50%)',
                                                    border: 'none',
                                                    background: 'none',
                                                    padding: '0',
                                                    zIndex: '10',
                                                    color: '#6c757d'
                                                }}
                                                onClick={() => togglePasswordVisibility('confirmPassword')}
                                            >
                                                <MDBIcon 
                                                    icon={showConfirmPassword ? 'eye-slash' : 'eye'} 
                                                    size="lg"
                                                />
                                            </button>
                                        </div>
                                        {errors.confirmPassword && (
                                            <div className="text-danger small">
                                                <MDBIcon icon="exclamation-triangle" className="me-1" />
                                                {errors.confirmPassword}
                                            </div>
                                        )}
                                    </div>

                                    {/* Role Field */}
                                    {/* <div className="mb-4">
                                        <label className="form-label fw-bold text-dark">
                                            <MDBIcon icon="user-tag" className="me-2 text-primary" />
                                            Vai tr
                                        </label>
                                        <select 
                                            className="form-select"
                                            name="role"
                                            value={formData.role}
                                            onChange={handleInputChange}
                                        >
                                            <option value="USER">Ngi dng (USER)</option>
                                        </select>
                                        <small className="text-muted">
                                            Ti khon mi s c quyn ngi dng
                                        </small>
                                    </div> */}

                                    {/* Terms and Conditions */}
                                    <div className="mb-4">
                                        <div className="alert alert-info small d-flex align-items-start">
                                            <MDBIcon icon="info-circle" className="me-2 mt-1" />
                                            <div>
                                                Bng cch ng k, bn ng  vi 
                                                <Link to="#" className="text-decoration-none fw-bold ms-1">
                                                    iu khon s dng
                                                </Link> v 
                                                <Link to="#" className="text-decoration-none fw-bold ms-1">
                                                    Chnh sch bo mt
                                                </Link> ca chng ti.
                                            </div>
                                        </div>
                                    </div>

                                    {/* Submit Button */}
                                    <MDBBtn 
                                        className='w-100 mb-4 py-3' 
                                        size='lg' 
                                        type="submit"
                                        disabled={isLoading}
                                        style={{ borderRadius: '10px', fontWeight: '600' }}
                                    >
                                        {isLoading ? (
                                            <>
                                                <MDBSpinner size="sm" className="me-2" />
                                                ang to ti khon...
                                            </>
                                        ) : (
                                            <>
                                                <MDBIcon icon="user-plus" className="me-2" />
                                                ng K Ngay
                                            </>
                                        )}
                                    </MDBBtn>
                                </form>

                                {/* Login Link */}
                                <div className="text-center">
                                    <p className="text-muted mb-0">
                                         c ti khon? 
                                        <Link to="/login" className="text-primary text-decoration-none fw-bold ms-1">
                                            ng nhp ngay
                                        </Link>
                                    </p>
                                </div>
                            </MDBCardBody>
                        </MDBCard>

                        {/* Features Card */}
                        <MDBCard className="mt-4 shadow-sm" style={{ borderRadius: '15px' }}>
                            <MDBCardBody className="p-4">
                                <h5 className="text-center text-primary mb-3">
                                    <MDBIcon icon="gift" className="me-2" />
                                    Li ch khi ng k
                                </h5>
                                <MDBRow>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="images" size="2x" className="text-primary mb-2" />
                                        <h6 className="text-dark">To nh min ph</h6>
                                        <small className="text-muted">To nh vi AI khng gii hn</small>
                                    </MDBCol>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="cloud" size="2x" className="text-success mb-2" />
                                        <h6 className="text-dark">Lu tr m my</h6>
                                        <small className="text-muted">Lu nh trn Cloudinary</small>
                                    </MDBCol>
                                    <MDBCol md="4" className="text-center mb-3">
                                        <MDBIcon icon="shield-alt" size="2x" className="text-info mb-2" />
                                        <h6 className="text-dark">Bo mt cao</h6>
                                        <small className="text-muted">M ha mt khu an ton</small>
                                    </MDBCol>
                                </MDBRow>
                            </MDBCardBody>
                        </MDBCard>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </div>
    );
}

export default SaveAccount;
